const {Server} = require('koush-utils/lib/socket/socket');
const AudioQueue = require('./audio-queue');
const DummySocket = require('koush-utils/lib/socket/dummysocket');
const {appendBuffer, Success, hexToBuffer, writeLine, readLenLoop, arrayBufferToBase64, writeLenData, makeUUID} = require('koush-utils/lib/base/util');
const {parseRTPPacket} = require('./rtp');

function CastServer() {
  // this.clients = [];

  var audioListener = this.audioListener = new Server();
  audioListener.listen({
    port: 0,
    address: '127.0.0.1'
  }, function(socket) {
    // this.clients.push(socket);
    // socket.onClose = function() {
    //   var index = this.clients.indexOf(socket);
    //   if (index != -1) {
    //     this.clients.splice(index, 1);
    //     socket.destroy();
    //   }
    // }.bind(this);

    socket.bufferWrites = true;
    readLenLoop(socket, function(data) {
      if (!data)
        return;
      var msg = $protobuf.roots.default.cast_channel.CastMessage.decode(data);
      var json;
      if (msg.payloadUtf8)
        json = JSON.parse(msg.payloadUtf8);

      console.log('received', msg, json);

      var handler = this.onMessage[msg.namespace];
      if (!handler) {
        console.error('unhandled cast message', msg)
        return;
      }
      handler.bind(this)(socket, msg, json).async();
    }.bind(this));
  }.bind(this),
  function() {
    if (this.onListening) {
      this.onListening();
    }
  }.bind(this));

  this.castId = "fb720c4cbabca202da80e378fc8474dee136e04f";
}

CastServer.prototype.sendUDP = function(address, port, bytes) {
  var prefix = new DataView(new ArrayBuffer(4 + 4 + address.byteLength + 4));
  prefix.setUint32(0, prefix.byteLength - 4 + bytes.byteLength);
  prefix.setUint32(4, address.byteLength);
  for (var i = 0; i < address.byteLength; i++) {
    prefix.setUint8(4 + 4 + i, address[i]);
  }
  prefix.setUint32(4 + 4 + address.byteLength, port);

  var buffer = appendBuffer(new Uint8Array(prefix.buffer, prefix.byteOffset, prefix.byteLength),
    new Uint8Array(bytes.buffer, bytes.byteOffset, bytes.byteLength));
  this.udpSocket.write(buffer);
}

CastServer.prototype.resolveFile = function(filename) {
  return '/assets/' + MD5(filename);
}

CastServer.prototype.resolveAjax = function*(filename) {
  return yield $.ajax({
            url: this.resolveFile(filename),
            type: "GET",
            dataType: 'binary',
            responseType: 'arraybuffer',
            processData: false,
            success: yield Success,
            error: yield Error,
  });
}

CastServer.prototype.stop = function() {
  // for (var i = 0; i < this.clients.length; i++) {
  //   this.clients[i].destroy();
  // }
  // this.clients = [];

  if (this.audioQueue) {
    this.audioQueue.stop();
    this.audioQueue = null;
  }
  if (this.udpSocket != null) {
    this.udpSocket.destroy();
    this.udpSocket = null;
  }

  this.sessionUUID = null;

  if (this.onStop)
    this.onStop();
}

CastServer.prototype.timeout = function() {
  delete this.rtpTimeout;
  this.stop();
}

CastServer.prototype.resetTimeout = function(timeout) {
  timeout = timeout || 10000;
  clearTimeout(this.rtpTimeout);
  // timeout after 10 seconds of no packets...
  // better way to do this on slow as shit devices?
  this.rtpTimeout = setTimeout(this.timeout.bind(this), timeout);
}

CastServer.prototype.connectUDP = function(adbSocketFactory, context) {
  this.stop();
  if (this.onStart)
    this.onStart();
  this.resetTimeout(60000);

  this.udpSocket = new DummySocket();
  this.context = context;

  var rtpAckBytes = hexToBuffer('8fce000400000002000000014341535400000064')
  var rtpReceiverReportBytes = hexToBuffer('81c90007000000020000000100000000000000ef0000000027bfaff00000000080cf0004000000020400000283aff46335954e18');

  var lastSequenceNumber = 0;
  var rolloverCount = 0;

  var lastAck = 0;
  var onRtpPacket = function(addr, port, packet) {
    this.resetTimeout();

    // if the last (typically highest) sequence number is WAY higher
    // than the incoming packet, that means that the packet id probably rolled over 2^16
    // this needs to be tracked (as far as I can tell, as I don't see a 32 bit integer anywhere)
    // this functions as a hacky 32 bit counter for the aesIvMask
    if (packet.id < lastSequenceNumber - (1 << 15))
      rolloverCount++;

    lastSequenceNumber = packet.id;

    // var now = Date.now();
    // if (now > lastAck + 100) {
    //   lastAck = now;
    //   var ack = rtpAckBytes.slice(0);
    //   ack = new DataView(ack)
    //   ack.setUint8(ack.byteLength - 4, packet.id % 256);
    //   this.sendUDP(addr, port, ack)
    // }


    var maski = 11;
    var mask = new Uint8Array(this.aesIvMask).slice(0);
    mask[maski] ^= packet.id & 0x00ff;
    mask[maski - 1] ^= (packet.id >> 8) & 0x00ff;
    // there seems to be an issue after 8 rollovers... are only 3 more bits being used for the aes counter?
    mask[maski - 2] ^= rolloverCount % 8;

    var useLegacy = false;

    // var start = Date.now();
    var withDecryptedBytes = function(decryptedBytes) {
      // console.log('decryption', Date.now() - start);
      // console.log(decryptedBytes);

      // used for flv export siphon
      this.writeAudio((rolloverCount << 16) + packet.id, decryptedBytes.slice(0))
      var debugInfo = {
        aesKey: this.aesKey,
        aesIvMask: this.aesIvMask,
        packet: packet,
        rolloverCount: rolloverCount,
      }

      this.audioQueue.queue(packet.id, decryptedBytes, debugInfo);
    }.bind(this)

    if (useLegacy) {
      var aesCtr = new aesjs.ModeOfOperation.ctr(new Uint8Array(this.aesKey), new aesjs.Counter(mask));
      var decryptedBytes = aesCtr.decrypt(new Uint8Array(packet.payload.slice(10)));
      withDecryptedBytes(decryptedBytes);
      return;
    }

    window.crypto.subtle.decrypt(
      {
        name: "AES-CTR",
        counter: mask,
        length: 16
      },
      this.cryptoAesKey,
      packet.payload.slice(10)
    ).then(ab => withDecryptedBytes(new Uint8Array(ab)))
  }.bind(this);

  var onRtcpPacket = function(addr, port, data) {
    var ssrc = data.getUint32(4)
    var lastSRTimestamp = data.getUint32(10)

    var report = rtpReceiverReportBytes.slice(0);
    report = new DataView(report)
    report.setUint32(4, ssrc + 1)
    report.setUint32(8, ssrc)
    report.setUint32(16, lastSequenceNumber)
    report.setUint32(24, lastSRTimestamp)

    report.setUint32(report.byteLength - 4, new Date().getTime() % 4294967295)
    report.setUint32(report.byteLength - 16, ssrc + 1)

    this.sendUDP(addr, port, report);
  }.bind(this);

  var onPacket = function(packet) {
    var view = new DataView(packet.buffer, packet.byteOffset, packet.byteLength);
    var addrLen = view.getInt32(0);
    var addr = packet.slice(4, 4 + addrLen);
    var port = view.getInt32(4 + addrLen);
    var data = packet.slice(4 + addrLen + 4);

    // console.log('udp packet', addr, port, packet);
    var dataView = new DataView(data.buffer, data.byteOffset, data.byteLength);
    var rtpPacket = parseRTPPacket(dataView)
    // console.log('rtp packet', rtpPacket);
    if (rtpPacket.marker)
      onRtcpPacket(addr, port, dataView);
    else
      onRtpPacket(addr, port, rtpPacket);
  }.bind(this);


  function* internal() {
    var now = new Date;
    var timestamp = now.getUTCFullYear() + (now.getUTCMonth() + 1).pad(2) + now.getUTCDate().pad(2) + '000000Z';
    try {
      var key = yield this.resolveAjax(timestamp + '.key.der');
      var cert = yield this.resolveAjax(timestamp + '.der');

      var socket = yield adbSocketFactory.newSocket('tcp:53519', yield);
      if (!socket)
        throw new Error('unable to connect to audio socket');

      this.udpSocket = socket;
      var audioQueue = this.audioQueue = new AudioQueue(context);
      audioQueue.sampleRate = this.sampleRate;
      audioQueue.numberOfChannels = this.numberOfChannels;

      var cleanupSelf = function(debugInfo) {
        // if (debugInfo)
          console.error('error decoding', debugInfo, arguments);

        if (this.audioQueue == audioQueue)
          this.audioQueue = null;
        audioQueue.stop();

        if (this.udpSocket == socket)
          this.udpSocket = null;
        socket.destroy();
      }.bind(this);

      audioQueue.onError = cleanupSelf;
      socket.onClose = cleanupSelf;

      socket.bufferWrites = true;
      yield writeLine(socket, password, yield);
      yield writeLine(socket, arrayBufferToBase64(key), yield);
      yield writeLine(socket, arrayBufferToBase64(cert), yield);
      yield writeLine(socket, this.castId, yield);

      readLenLoop(socket, onPacket);
    }
    catch (e) {
      console.error('error connecting to udp', e);
    }
  }

  internal.bind(this)().async();
}

CastServer.prototype.getPort = function(socket, message) {
  return this.audioListener.localPort;
}

CastServer.prototype.respond = function(socket, message, payload) {
  return this.sendMessage(socket, message.destinationId, message.sourceId, message.namespace, payload);
}

CastServer.prototype.sendMessage = function(socket, sourceId, destinationId, namespace, payload) {
  if (payload.constructor.name == 'Object')
    payload = JSON.stringify(payload);

  var message = {
    protocolVersion: 0, // CASTV2_1_0
    sourceId: sourceId,
    destinationId: destinationId,
    namespace: namespace,
    payloadType: payload.constructor.name === 'String' ? 0 : 1,
  }

  if (message.payloadType)
    message.payloadBinary = payload.constructor.encode(payload).finish();
  else
    message.payloadUtf8 = payload;

  // console.log('sent', message);

  var bytes = $protobuf.roots.default.cast_channel.CastMessage.encode(message).finish();
  writeLenData(socket, bytes.buffer.slice(bytes.byteOffset, bytes.byteOffset + bytes.byteLength))
}

CastServer.prototype.onMessage = {};

CastServer.prototype.onMessage['urn:x-cast:com.google.cast.tp.deviceauth'] = function*(socket, message) {
  try {
    var now = new Date;
    var timestamp = now.getUTCFullYear() + (now.getUTCMonth() + 1).pad(2) + now.getUTCDate().pad(2) + '000000Z';

    var sig = yield this.resolveAjax(timestamp + '.sig');
    var cert = yield this.resolveAjax('cert-aa.der');
    var ca1 = yield this.resolveAjax('cert-ab.der');
    var ca2 = yield this.resolveAjax('cert-ac.der');
    var ca3 = yield this.resolveAjax('cert-ad.der');

    sig = new Uint8Array(sig);
    cert = new Uint8Array(cert);
    ca1 = new Uint8Array(ca1);
    ca2 = new Uint8Array(ca2);
    ca3 = new Uint8Array(ca3);

    var response = {
      response: {
        signature: sig,
        clientAuthCertificate: cert,
        intermediateCertificate: [ ca1, ca2, ca3 ],
        signatureAlgorithm: 1,
        senderNonce: null,
        hashAlgorithm: 0,
        crl: null,
      },
      error: null,
    }

    this.respond(socket, message, $protobuf.roots.default.cast_channel.DeviceAuthMessage.fromObject(response));
  }
  catch (e) {
    console.error('error getting certs', e)
  }
}

CastServer.prototype.onJSONMessage = {};

CastServer.prototype.onJSONMessage['GET_APP_AVAILABILITY'] = function*(socket, message, json) {
  var status = {
    "availability": {
    },
    "requestId": json.requestId,
    "responseType": "GET_APP_AVAILABILITY"
  }

  $.each(json.appId, function(index, appId) {
    status.availability[appId] = 'APP_UNAVAILABLE';
  })

  status['8E6C866D'] = 'APP_AVAILABLE';
  // status['CC1AD845'] = 'APP_AVAILABLE';

  this.respond(socket, message, status);
}

CastServer.prototype.onJSONMessage['STOP'] = function*(socket, message, json) {
  this.sessionUUID = null;

  yield this.onJSONMessage['RECEIVER_STATUS'].bind(this)(socket, message, json, true).async();
}

CastServer.prototype.onJSONMessage['LAUNCH'] = function*(socket, message, json) {
  this.sessionUUID = makeUUID();
  yield this.onJSONMessage['RECEIVER_STATUS'].bind(this)(socket, message, json, true).async();
}

CastServer.prototype.onJSONMessage['GET_STATUS'] = function*(socket, message, json) {
  yield this.onJSONMessage['RECEIVER_STATUS'].bind(this)(socket, message, json).async();
}

