CastServer.prototype.onMessage['urn:x-cast:com.google.cast.receiver'] = function*(socket, message, json) {
  var handler = this.onJSONMessage[json.type];
  if (!handler) {
    console.error('unhandled cast json message', json);
    return;
  }

  yield handler.bind(this)(socket, message, json).async();
}

CastServer.prototype.onMessage['urn:x-cast:com.google.cast.media'] = CastServer.prototype.onMessage['urn:x-cast:com.google.cast.receiver'];

CastServer.prototype.onMessage['urn:x-cast:com.google.cast.tp.heartbeat'] = function*(socket, message) {
  this.respond(socket, message, {
    type: "PONG"
  });
}

CastServer.prototype.onMessage['urn:x-cast:com.google.cast.tp.connection'] = function*(socket, message) {
  this.respond(socket, message, {
    protocolVersion:0,
    type: "CONNECTED"
  });
}

CastServer.prototype.onMessage['urn:x-cast:com.google.cast.receiver.discovery'] = function*(socket, message, json) {
  var status = {
    "controlNotifications": 1,
    "deviceCapabilities": 2052,
    "deviceIconUrl": "/setup/icon.png",
    "deviceId": this.castId,
    "deviceModel": "Google Home",
    "friendlyName": "Kitchen",
    "receiverMetricsId": "5C1CC5224680F4B2",
    "requestId": json.requestId,
    "type": "DEVICE_INFO",
    "wifiProximityId": "FA8FCA7A7F1C"
  }

  this.respond(socket, message, status);
}

CastServer.prototype.onMessage['urn:x-cast:com.google.cast.webrtc'] = function*(socket, message, json) {
  for (var stream in json.offer.supportedStreams) {
    stream = json.offer.supportedStreams[stream];
    console.log('audio offer:', stream);
    this.sampleRate = stream.sampleRate;
    this.numberOfChannels = stream.channels;
    if (this.audioQueue != null) {
      var audioQueue = this.audioQueue;
      audioQueue.sampleRate = this.sampleRate;
      audioQueue.numberOfChannels = this.numberOfChannels;
    }

    if (stream.sampleRate != 48000)
      console.warn('Offered sample rate is not 48000?', stream.sampleRate);
    if (stream.channels != 2)
      console.warn('Offered channels is not 2?', stream.channels);
  }

  var answer = {
    "answer": {
      "castMode": "mirroring",
      "receiverRtcpEventLog": [0],
      "rtpExtensions": [
        ["adaptive_playout_delay"]
      ],
      "sendIndexes": [0],
      "ssrcs": [2],
      "udpPort": 8010
    },
    "result": "ok",
    "seqNum": 0,
    "type": "ANSWER"
  }

  this.aesKey = hexToBuffer(json.offer.supportedStreams[0].aesKey)
  this.aesIvMask = hexToBuffer(json.offer.supportedStreams[0].aesIvMask);

  crypto.subtle.importKey('raw', this.aesKey, 'AES-CTR', false, ['decrypt'])
  .then(key => {
    this.cryptoAesKey = key;
    this.respond(socket, message, answer);
  });
}

CastServer.prototype.siphon = function(dump) {
  this.pendingAudio = [];
  this.audioDump = dump;
}

CastServer.prototype.writeAudio = function(id, decryptedBytes) {
  if (!this.audioDump)
    return;
  this.pendingAudio.push({
    id,
    decryptedBytes,
  })

  this.pendingAudio.sort(function(a, b) {
    return a.id - b.id;
  });

  var keep = this.pendingAudio.splice(0, 50);
  var send = this.pendingAudio;
  this.pendingAudio = keep;

  for (var i = 0; i < send.length; i++) {
    this.audioDump.dataReceived(send[i].decryptedBytes);
  }
}

module.exports = CastServer;