GcmRtcManager.prototype.setupPeerConnection = function(type, senderId, registrationId, dstPort, src, srcPort, getDesc) {
  var pc = new RTCPeerConnection(this.rtcc);
  var token;
  var sendConnect = function(candidates) {
    var portion = [];

    var desc = getDesc();
    var sdp = desc.sdp;
    var compressedDesc = {
      type: desc.type,
      sdp: GcmRtcManager.compressSdp(desc.sdp),
    }
    var descLen = JSON.stringify(compressedDesc).length;

    // gaurantee that the desc is sent at least once.
    var sentOnce;
    for (var candidate in candidates) {
      candidate = candidates[candidate];
      if (candidate == null)
        continue;
      portion.push(candidate);
      if (descLen + JSON.stringify(portion).length > 3200) {
        sentOnce = true;
        this.sendGcm(senderId, registrationId, dstPort, src, srcPort, type,
        {
          desc: compressedDesc,
          candidates: portion
        });
        portion = [];
      }
    }

    if (portion.length > 0 || !sentOnce) {
      this.sendGcm(senderId, registrationId, dstPort, src, srcPort, type,
      {
        desc: compressedDesc,
        candidates: portion
      });
    }
  }.bind(this);

  pc.onicecandidate = function (evt) {
    // uncomment to force TURN
    // if (evt.candidate.candidate.indexOf('relay') == -1)
    //   return;
    if (evt.candidate) {
      console.log('candidate', evt.candidate);
      token = throttleTimeout(token, evt.candidate, 500, sendConnect);
    }
    else {
      console.log('done sending ice candidates');
    }
  }.bind(this);

  var key = GcmRtcManager.getKey(registrationId, dstPort, srcPort);
  var conn = new GcmRtcConnection(this, pc, key);
  conn.sendConnect = sendConnect;

  pc.onsignalingstatechange = function(ev) {
    // console.log('pcs', pc.signalingState)
    if (pc.signalingState == 'stable') {
      if (this.gcmRtcConnections[key] == conn) {
        // keep allowing ice candidates?
        // delete this.gcmRtcConnections[key];
      }
    }
    else if (pc.signalingState == 'closed') {
      conn.destroy();
    }
  }.bind(this);

  this.gcmRtcConnections[key] = conn;
  return conn;
}

GcmRtcManager.prototype.connect = function(options) {
  return new Promise((resolve, reject) => {

    var senderId = options.senderId;
    var registrationId = options.registrationId;
    var port = options.port;

    if (!registrationId) {
      throw new Error('registrationId was null on connect');
    }

    // ports can be any old random string
    var localPort = Math.random().toString(16);
    var d;
    var conn = this.setupPeerConnection('offer', senderId, registrationId, port, this.registrationId, localPort, function() {
      return d;
    });
    var pc = conn.peerConnection;
    try {
      if (navigator.userAgent.indexOf('Safari') != -1 && navigator.userAgent.indexOf('Chrome') == -1 && options.offerToReceiveAudio && options.offerToReceiveVideo) {
        pc.addTransceiver('audio');
        pc.addTransceiver('video');
      }
    }
    catch (e) {
    }

    var failureTimeout = setTimeout(function() {
      conn.destroy();
      reject(new Error('Timeout waiting for RTC Connection'));
    }, 30000);

    function doOffer(options) {
      pc.createOffer(options).then(function(desc) {
        d = desc;
        console.log(desc);
        pc.setLocalDescription(desc);
        // force the desc to be sent. safari doesn't trigger onicecandidate on receive only?
        conn.sendConnect([]);
      });
    }

    if (options.offerToReceiveAudio || options.offerToReceiveVideo || options.audio) {
      function internal() {
        pc.ontrack = function(e) {
          conn.streams = e.streams;
          console.log('got the remote stream');
          clearTimeout(failureTimeout);
          resolve(conn);
        }
        doOffer({
          offerToReceiveAudio: !!options.offerToReceiveAudio,
          offerToReceiveVideo: !!options.offerToReceiveVideo,
          voiceActivityDetection: false
        });
      }

      if (!options.audio) {
        internal();
        return;
      }

      navigator.mediaDevices.getUserMedia({ "audio": true })
      .then(function(stream) {
        stream.getTracks().forEach(function(track) {
          pc.addTrack(track, stream);
        });
        internal();
      })
      .catch(e => {
        console.error('audio fail', e);
        internal();
      });
    }
    else {
      var pinger = conn.prepareChannel('pinger');
      pinger.onopen = function() {
        console.log('got rtc pinger')
        conn.setupPinger(pinger);
        clearTimeout(failureTimeout);
        resolve(conn);
      }
      conn.listenSockets();
      doOffer({});
    }

  });
}

GcmRtcManager.prototype.isListening = function(port) {
  return this.gcmRtcListeners[port] != null;
}
GcmRtcManager.prototype.stopListen = function(port) {
  delete this.gcmRtcListeners[port];
}

GcmRtcManager.prototype.listen = function(port, cb) {
  if (this.gcmRtcListeners[port]) {
    console.log('already listening on gcm port ' + port)
    return;
  }
  this.gcmRtcListeners[port] = {
    listener: this,
    listenCallback: cb
  };
}

GcmRtcManager.prototype.incoming = function(senderId, src, srcPort, dst, dstPort, message, listenCallback) {
  var key = GcmRtcManager.getKey(src, srcPort, dstPort);
  var conn = this.gcmRtcConnections[key];
  if (!conn) {
    if (!src) {
      console.warn('received null registraition on incoming message. ignoring');
      return;
    }

    // new connection
    var d;
    conn = this.setupPeerConnection('answer', senderId, src, srcPort, dst, dstPort, function() {
      return d;
    });
    var sdp = message.desc.sdp;
    message.desc.sdp = GcmRtcManager.decompressSdp(sdp);
    conn.remoteDesc = new RTCSessionDescription(message.desc);
    var pc = conn.peerConnection;
    pc.ondatachannel = function(ev) {
      console.log('got rtc pinger')
      this.setupPinger(ev.channel);
      listenCallback(conn);
      this.listenSockets();
    }.bind(conn);
    pc.setRemoteDescription(conn.remoteDesc, function() {
      pc.createAnswer()
      .then(function(answer) {
        d = answer;
        pc.setLocalDescription(answer);
      }, function() {
        console.error('answer error', arguments);
      })
    });
  }
  else if (!conn.remoteDesc) {
    var sdp = message.desc.sdp;
    message.desc.sdp = GcmRtcManager.decompressSdp(sdp);
    conn.remoteDesc = new RTCSessionDescription(message.desc);
    var pc = conn.peerConnection;
    pc.setRemoteDescription(conn.remoteDesc);
  }
  else {
    // console.log('more ice candidates received')
  }

  conn.addCandidates(message);
}

exports.GcmRtcSocket = GcmRtcSocket;
exports.GcmRtcManager = GcmRtcManager;
