;
function parseRTPPacket(buffer) {
  const buffer0 = buffer.getUint8(0);
  const buffer1 = buffer.getUint8(1);
  const hasExtensions = (buffer0 >> 4) & 0x01;
  const marker = (buffer1) >>> 7;
  const num_csrc_identifiers = (buffer0 & 0x0F);

  const payloadOffset = (num_csrc_identifiers * 4) + (hasExtensions ? 16 : 12);
  const payload = buffer.buffer.slice(buffer.byteOffset + payloadOffset);

  return {
    id: buffer.getUint16(2),
    timestamp: buffer.getUint32(4),
    ssrc: buffer.getUint32(8),
    length: payload.byteLength,
    marker,
    payload,
  };
}

function parseRTCPPacket(buffer) {
  const buffer1 = buffer.getUint8(1);
  const packetType = buffer1;
  const timestamp = buffer.getUint32(16);

  return {
    timestamp,
    packetType,
    buffer,
    get payload() {
      console.log("yellowstone: Please don't use payload. To remove confusion, this was renamed to simply 'buffer'.");
      return buffer;
    }
  };
}

function parseTransport(transport) {
  const obj = {};
  const parts = transport.split(";");
  const protocol = parts[0];

  for (let i = 0; i < parts.length; i++) {
    const part = parts[i];
    const index = part.indexOf("=");

    if (index > -1 && index !== part.length - 1) {
      obj[part.substring(0, index)] = part.substring(index + 1);
    }
  }

  return {
    protocol,
    parameters: obj
  };
}

function generateSSRC() {
  return getRandomIntInclusive(1,0xffffffff);
}

function getRandomIntInclusive(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

exports.parseRTPPacket = parseRTPPacket;