function showModal(opts) {
  var modal = $('#notificationModal');
  var ok = modal.find('#modal-ok');
  var cancel = modal.find('#modal-cancel');

  ok.unbind('click');
  cancel.unbind('click');
  modal.unbind('hidden.bs.modal');

  opts.cancelButton = opts.cancelButton || 'Cancel';
  opts.okButton = opts.okButton || 'OK';
  opts.title = opts.title || chrome.runtime.getManifest().name;
  opts.body = opts.body || '';

  if (opts.hideCancel)
    cancel.hide();
  else
    cancel.show();

  ok.text(opts.okButton);
  cancel.text(opts.cancelButton);
  modal.find('#modal-title').text(opts.title);
  modal.find('#modal-body').html(opts.body);

  var clickedOk;
  ok.click(function() {
    clickedOk = true;
    if (!opts.ok || !opts.ok())
      $('#notificationModal').modal('hide');
  });

  if (opts.cancel) {
    modal.on('hidden.bs.modal', function() {
      if (!clickedOk)
        opts.cancel();
    })
    cancel.click(opts.cancel);
  }

  $('#notificationModal').modal();

  if (opts.duration) {
    setTimeout(function() {
      $('#notificationModal').modal('hide');
    }, opts.duration)
  }
}

function shortModal(title, body) {
  showModal({
    title: title,
    body: body,
    duration: 8000,
    hideCancel: true
  })
}

exports.showModal = showModal;
exports.shortModal = shortModal;
