const {Socket} = require('../lib/socket/socket');
const {ab2str, str2ab, make4Len16, readString} = require('../lib/base/util');
const DummySocket = require('../lib/socket/dummysocket');

var Adb = {};

Adb.sendHostCommand = function(command, cb) {
  Socket.connect({host: '127.0.0.1', port: 5037}, function(socket) {
    if (!socket) {
      cb();
      return;
    }
    var hostCommand = command;
    command = make4Len16(command.length) + command;

    socket.read(4, function(ok) {
      var result = ab2str(ok);
      if (result != 'OKAY') {
        console.error('error in response to adb host command', hostCommand, result)
        socket.destroy();
        cb();
        return;
      }

      socket.read(4, function(len) {
        var lenStr = ab2str(len);
        len = parseInt(lenStr, 16);
        if (len == 0 || lenStr == 'OKAY') {
          cb(socket, new ArrayBuffer(0));
          return;
        }
        socket.read(len, function(data) {
          cb(socket, data);
        });
      })
    })

    socket.write(str2ab(command), function() {})
  });
}

Adb.devices = function(cb) {
  var adbDevices = {};
  function parseConnectionPayload(payload) {
    var rawPayload = payload;
    payload = payload.replace('\t', ' ');
    var i = payload.indexOf(' ');
    if (i == -1)
      return;

    var serialno = payload.substring(0, i);

    payload = payload.substring(i).trim();
    var newPayload;
    while (newPayload != payload) {
      newPayload = payload;
      payload = payload.replace('  ', ' ');
    }

    var values = {};
    var firstSpace = payload.indexOf(' ');
    if (firstSpace == -1)
      firstSpace = payload.length
    var status = payload.substring(0, firstSpace);
    payload = payload.substring(firstSpace + 1);
    while (payload.length) {
      i = payload.indexOf(':');
      if (i == -1)
        break;
      var key = payload.substring(0, i);
      var rest = payload.substring(i + 1);
      var nextSpace = rest.indexOf(' ');
      var nextColon = rest.indexOf(':');
      var value;
      if (nextSpace == -1 || nextColon == -1) {
        value = rest;
        payload = '';
      }
      else {
        while (nextSpace != -1 && nextSpace < nextColon) {
          value = rest.substring(0, nextSpace);
          payload = rest.substring(nextSpace + 1);
          nextSpace = rest.indexOf(' ', nextSpace + 1);
        }
      }
      values[key] = value;
    }

    // filter?
    var name;
    if (!values['model'])
      name = serialno;
    else
      name = values['model'].replace('_', ' ');

    adbDevices[serialno] = {
      serialno: serialno,
      name: name,
      status: status,
      properties: rawPayload
    }
  }

  Adb.sendHostCommand('host:devices-l', function(socket, data) {
    if (!socket) {
      cb();
      return;
    }
    socket.destroy();
    data = ab2str(data);
    console.log('ADB devices:', data);
    // console.log(data);
    data = data.trim();
    var lines = data.split('\n');
    for (var line in lines) {
      line = lines[line];
      parseConnectionPayload(line);
    }
    console.log('parsed ADB devices:', adbDevices);
    cb(adbDevices);
  });
}

Adb.killServer = function(cb) {
  Adb.sendHostCommand('host:kill-server', function(socket, data) {
    if (!socket) {
      cb();
      return;
    }
    socket.destroy();
    data = ab2str(data);
    // console.log(data);
    if (cb)
      cb();
  });
}

Adb.sendClientCommand = function(options, cb) {
  var command = options.command;
  var serialno = options.serialno;

  Socket.connect({host: '127.0.0.1', port: 5037}, function(socket) {
    if (!socket) {
      cb();
      return;
    }
    socket.read(4, function(data) {
      var result = ab2str(data);
      if (result != 'OKAY') {
        socket.destroy();
        cb(null);
        return;
      }

      var clientCommand = command;
      clientCommand = make4Len16(clientCommand.length) + clientCommand;
      socket.read(4, function(data) {
        var result = ab2str(data);
        if (result != 'OKAY') {
          socket.destroy();
          cb(null);
          return;
        }
        cb(socket);
      });

      socket.write(str2ab(clientCommand), function() {});
    })

    var hostCommand = 'host:transport:' + serialno;
    hostCommand = make4Len16(hostCommand.length) + hostCommand;
    socket.write(str2ab(hostCommand), function() {})
  });
}

Adb.shell = function(options, cb) {
  var command = options.command;
  var serialno = options.serialno;

  Adb.getOrCreateSockFactory(options).newSocket('shell:' + command,
  function(socket) {
    if (!socket) {
      cb();
      return;
    }
    readString(socket, function(str) {
      cb(str);
    });
  });
}


Adb.forward = function(options, cb) {
  // host:forward:tcp:5000;tcp:5000
  var command = 'host-serial:' + options.serialno + ':forward:' + options.from + ';' + options.to;
  Adb.sendHostCommand(command, function(socket, err) {
    if (socket)
      socket.destroy();
    cb(socket, err);
  })
}


Adb.reverse = function(options, cb) {
  // host:forward:tcp:5000;tcp:5000
  var command = 'reverse:forward:' + options.from + ';' + options.to;
  Adb.sendClientCommand({
    serialno: options.serialno,
    command: command
  }, function(socket, err) {
    if (socket)
      readString(socket, cb);
    else 
      cb();
  })
}


function AdbSync() {
}
AdbSync.MKID = function(a,b,c,d) { return  ((a.charCodeAt(0)) | ((b.charCodeAt(0)) << 8) | ((c.charCodeAt(0)) << 16) | ((d.charCodeAt(0)) << 24)) }
AdbSync.ID_RECV = AdbSync.MKID('R','E','C','V')
AdbSync.ID_SEND = AdbSync.MKID('S','E','N','D')
AdbSync.ID_DONE = AdbSync.MKID('D','O','N','E')
AdbSync.ID_DATA = AdbSync.MKID('D','A','T','A')
AdbSync.DATA_MAX = 64 * 1024;

Adb.pull = function(options, cb) {
  var file = options.file;
  var serialno = options.serialno;
  var fileEntry = options.fileEntry;
  var output = options.socket;

  if (!output) {
    (function() {
      var fileWriter;

      output = {
        write: function(data, cb) {
          if (!fileWriter) {
              fileEntry.createWriter(function(fw) {
                fileWriter = fw;
                output.write(data, cb);
              });
              return;
          }
          fileWriter.onwriteend = cb;
          fileWriter.write(new Blob([data]));
        }
      }
    })();
  }

  var dummy = new DummySocket();
  Socket.pump(dummy, output, function() {
    cb(fileEntry);
  });

  Adb.getOrCreateSockFactory(options).newSocket('sync:',
  function(socket) {
    if (!socket) {
      cb();
      return;
    }

    function readChunk(len) {
      socket.read(len, function(data) {
        dummy.dataReceived(data);
        readHeader();
      })
    }

    function readHeader() {
      socket.read(8, function(data) {
        var view = new DataView(data.buffer, data.byteOffset, data.byteLength);
        var id = view.getUint32(0, true);
        if (id == AdbSync.ID_DATA) {
          var len = view.getUint32(4, true);
          readChunk(len);
          return;
        }
        socket.destroy();
        if (id == AdbSync.ID_DONE) {
          dummy.dataReceived(null);
          return;
        }
        cb();
      })
    }

    var msg = new ArrayBuffer(8);
    var msgView = new DataView(msg);
    msgView.setUint32(0, AdbSync.ID_RECV, true);
    msgView.setUint32(4, file.length, true);

    socket.write(msg, function() {
      socket.write(str2ab(file), function() {
        readHeader();
      })
    });
  })
}

Adb.createSocketFactory = function(serialno) {
  return {
    newSocket: function(command, cb) {
      Adb.sendClientCommand({
        serialno: serialno,
        command: command
      },
      cb);
    }
  };
}

Adb.getOrCreateSockFactory = function(opts) {
  return opts.socketFactory || Adb.createSocketFactory(opts.serialno);
}

Adb.push = function(options, cb) {
  var file = options.file;
  var serialno = options.serialno;
  var fileSocket = options.socket;

  Adb.getOrCreateSockFactory(options).newSocket('sync:',
  function(socket) {
    if (!socket) {
      cb();
      return;
    }

    var msg = new ArrayBuffer(8);
    var msgView = new DataView(msg);
    var fileAndMode = file + ',0644';
    msgView.setUint32(0, AdbSync.ID_SEND, true);
    msgView.setUint32(4, fileAndMode.length, true);

    socket.write(msg, function() {
      socket.write(str2ab(fileAndMode), function() {
        var done;
        var writing = true;
        fileSocket.onClose = function() {
          var msg = new ArrayBuffer(8);
          var msgView = new DataView(msg);
          msgView.setUint32(0, AdbSync.ID_DONE, true);
          msgView.setUint32(4, 0, true);
          socket.write(msg, function() {
            // socket.destroy();
            // read back ID_OKAY
            socket.read(8, function() {
              cb();
            })
          });
        }

        function readChunk() {
          fileSocket.read(function(data) {
            if (data.byteLength > AdbSync.DATA_MAX) {
              var extra = data.subarray(AdbSync.DATA_MAX);
              data = data.subarray(0, AdbSync.DATA_MAX);
              fileSocket.unshift(extra);
            }
            writeChunk(data);
          })
        }

        function writeChunk(data) {
          var msg = new ArrayBuffer(8);
          var msgView = new DataView(msg);
          msgView.setUint32(0, AdbSync.ID_DATA, true)
          msgView.setUint32(4, data.byteLength, true)
          socket.write(msg, function() {
            var buffer = data.buffer;
            if (data.byteOffset || data.length != buffer.byteLength) {
              buffer = buffer.slice(data.byteOffset, data.byteOffset + data.byteLength);
            }
            socket.write(buffer, function() {
              readChunk();
            })
          })
        }

        readChunk();
      })
    });
  })
}

module.exports = Adb;
