function getAuthToken(interactive, cb) {
  if (!window.chrome || !window.chrome.identity) {
    console.error('no auth token implemented');
    process.nextTick(cb);
    return;
  }

  chrome.identity.getAuthToken({
    interactive: interactive,
    // must use the exact same scopes used to submit the license or the token shits
    scopes:
    ['https://www.googleapis.com/auth/userinfo.profile',
    'https://www.googleapis.com/auth/userinfo.email',
    'https://www.googleapis.com/auth/chromewebstore.readonly']
  },
  function(token) {
    if (!token)
      console.error('unable to get authToken', chrome.runtime.lastError)
    cb(token);
  });
}

var _isNode;
try {
  // node will never have naviagor... electron and browser will
  _isNode = !navigator;

}
catch (e) {
  _isNode = true;
}

function isNode() {
  return _isNode;
}

function isElectron() {
  return navigator.userAgent.indexOf('Electron') != -1;
}

function isBrowser() {
  return !window.chrome || !window.chrome.app || !window.chrome.app.window;
}

if (!isNode() && !isElectron()) {
  window.sharedGlobals = window;
}

(function() {
  if (isNode()) return;
  
  var current = '';

  function save(args) {
    try {
      for (var i in args) {
        if (args[i] && args[i].constructor != String)
          args[i] = JSON.stringify(args[i]);
      }
      current += args.join(' ') + '\n';
    }
    catch (e) {
    }
  }


  if (window.IS_RELEASE) {
    var log = console.log;
    var error = console.error;
    var warn = console.warn;
    var info = console.info;

    function wrap(f) {
      return function() {
        f.apply(console, arguments);
        save(Array.prototype.slice.call(arguments))
      }
    }

    console.error = wrap(console.error);
    console.log = wrap(console.log);
    console.warn = wrap(console.warn);
    console.info = wrap(console.info);
  }

  sharedGlobals.getConsoleLog = function(cb) {
    cb(current);
  }

  function getLogFromWindow(w) {
    return new Promise(function(resolve, reject) {
      if (!w.getConsoleLog) {
        resolve('getConsoleLog not found');
        return;
      }
      w.getConsoleLog(function(log) {
        resolve({
          content: log && log.length ? log : 'log is empty'
        });
      })
    })
  }

  window.gistConsoleLog = function(files, cb) {
    chrome.runtime.getBackgroundPage(function(background) {
      getLogFromWindow(background)
      .then(function(log) {
        files['background.txt'] = log;

        var windows = chrome.app.window.getAll();
        var promises = windows.map(function(w) {
          return getLogFromWindow(w.contentWindow)
          .then(function(log) {
            files['window-' + w.id + '.txt'] = log;
          })
        })

        return Promise.all(promises);
      })
      .then(function() {
        var gist = {
          "description": chrome.runtime.getManifest().name + ' console log',
          "public": false,
          "files": files
        }

        fetch('https://vysor.io/gist', {
          method: 'POST',
          body: JSON.stringify(gist)
        })
        .then(function(response) {
          response.json().then(function(json) {
            cb(json.html_url);
          })
        })
      })
    });
  }

})();

