const { Socket, Server } = require('../lib/socket/socket');
const { str2ab, ab2str, make4Len16, appendBuffer, hex2b64, showNotification } = require('../lib/base/util');

class ChromeUsbDevice {
  constructor(device) {
    this.device = device;
  }

  get vendorId() {
    return this.device.vendorId;
  }

  get productId() {
    return this.device.productId;
  }

  get serialNumber() {
    return this.device.serialNumber;
  }

  claimInterface(interfaceNumber) {
    return new Promise((resolve, reject) => {
      chrome.usb.claimInterface(this.connectionHandle, interfaceNumber, () => {
        var error = chrome.runtime.lastError;
        if (error) {
          reject(error);
          return;
        }
        resolve();
      });
    });
  }

  listInterfaces() {
    return new Promise((resolve, reject) => {
      chrome.usb.listInterfaces(this.connectionHandle, interfaces => {
        if (!interfaces) {
          reject(chrome.runtime.lastError);
          return;
        }

        resolve(interfaces);
      });
    });
  }

  open() {
    return new Promise((resolve, reject) => {
      chrome.usb.openDevice(this.device, connectionHandle => {
        this.connectionHandle = connectionHandle;

        if (!connectionHandle) {
          reject(chrome.runtime.lastError)
          return;
        }

        console.log('opened adb');
        resolve();
      })
    })
  }

  close() {
    chrome.usb.closeDevice(this.connectionHandle);
  }

  newTransport(iface) {
    return new AdbUsbTransport(this.connectionHandle, iface);
  }
}

function AdbUsbTransport (handle, iface) {
    this.handle = handle;
    this.iface = iface;
    this.type = 'usb';
    for (var endpoint in iface.endpoints) {
        endpoint = iface.endpoints[endpoint];
        if (endpoint.type == 'bulk') {
            this.zero_mask = endpoint.maximumPacketSize - 1;
            if (endpoint.direction == 'in') {
                this.in = endpoint;
            }
            else {
                this.out = endpoint;
            }
        }
    }
}

AdbUsbTransport.prototype.destroy = function () {
    chrome.usb.releaseInterface(this.handle, this.iface.interfaceNumber, function () {
        chrome.runtime.lastError;
        chrome.usb.closeDevice(this.handle, function () {
            chrome.runtime.lastError;
        })
    }.bind(this))
}

AdbUsbTransport.prototype.write = function (data, callback) {
    if (this.writing) {
        if (!this.pendingWrites)
            this.pendingWrites = [];
        this.pendingWrites.push({ data: data, callback: callback });
        return;
    }

    var transfer = {
        direction: "out",
        endpoint: this.out.address,
        data: data
    }

    this.writing = true;
    chrome.usb.bulkTransfer(this.handle, transfer, function (result) {
        chrome.runtime.lastError;
        this.writing = false;
        callback(result);
        if (!this.pendingWrites)
            return;
        var next = this.pendingWrites.shift();
        if (!this.pendingWrites.length)
            this.pendingWrites = null;
        this.write(next.data, next.callback);
    }.bind(this));
}

AdbUsbTransport.prototype.read = function (len, cb) {
    var transfer = {
        direction: "in",
        endpoint: this.in.address,
        length: len
    }
    chrome.usb.bulkTransfer(this.handle, transfer, function (result) {
        chrome.runtime.lastError;
        cb(result);
    });
}

function getUserSelectedDevices() {
  return new Promise((resolve, reject) => {
    chrome.usb.getUserSelectedDevices({
      filters: [
        {
        interfaceClass: 0xff,
        interfaceSubclass: 0x42,
        interfaceProtocol: 0x1,
        }
      ]
    }, function(devices) {
      resolve(devices.map(device => new ChromeUsbDevice(device)));
    });
  })
}

exports.getUserSelectedDevices = getUserSelectedDevices;
