async function getUserSelectedDevices () {
  let device = await navigator.usb.requestDevice({
    filters: [{
      classCode: 0xff,
      subclassCode: 0x42,
      protocolCode: 0x1,
    }]
  })
  return [new WebUsbDevice(device)]
}

class WebUsbDevice {
  constructor(device) {
    this.device = device;
  }

  get vendorId () {
    return this.device.vendorId;
  }

  get productId () {
    return this.device.productId;
  }

  get serialNumber () {
    return this.device.serialNumber;
  }

  async open () {
    await this.device.open()
  }

  async close () {
    await this.device.close();
  }

  async claimInterface (interfaceNumber) {
    return await this.device.claimInterface(interfaceNumber);
  }

  async listInterfaces () {
    if (this.device.configuration === null)
      await this.device.selectConfiguration(1);
    return this.device.configuration.interfaces.map(iface => {
      let alternate = iface.alternate || iface.alternates[0];
      let { interfaceClass,
        interfaceName,
        interfaceProtocol,
        interfaceSubclass,
        endpoints } = alternate;
      return {
        interfaceNumber: iface.interfaceNumber,
        interfaceClass, interfaceName, interfaceProtocol, interfaceSubclass, endpoints,
      }
    });
  }

  newTransport (iface) {
    return new AdbUsbTransport(this.device, iface);
  }
}

class AdbUsbTransport {
  constructor(device, iface) {
    this.device = device;
    this.iface = iface;

    for (var endpoint of iface.endpoints) {
      if (endpoint.type == 'bulk') {
        this.zero_mask = endpoint.packetSize - 1;
        if (endpoint.direction == 'in') {
          this.in = endpoint;
        }
        else {
          this.out = endpoint;
        }
      }
    }
  }

  destroy () {
    this.device.releaseInterface(iface.interfaceNumber);
  }

  async write (data, callback) {
    if (this.writing) {
      if (!this.pendingWrites)
        this.pendingWrites = [];
      this.pendingWrites.push({ data: data, callback: callback });
      return;
    }

    this.writing = true;
    var result
    try {
      result = await this.device.transferOut(this.out.endpointNumber, data);
    }
    catch (e) {
      result = {
        resultCode: e,
      }
    }
    finally {
      this.writing = false;
    }
    callback(result);

    if (!this.pendingWrites)
      return;
    var next = this.pendingWrites.shift();
    if (!this.pendingWrites.length)
      this.pendingWrites = null;
    this.write(next.data, next.callback);
  }

  async read (len, cb) {
    var result
    try {
      result = await this.device.transferIn(this.in.endpointNumber, len);
      result = {
        data: result.data.buffer,
      }
    }
    catch (e) {
      result = {
        resultCode: e,
      }
    }

    cb(result);
  }
}

exports.getUserSelectedDevices = getUserSelectedDevices;
