const PepperAudio = require('../pepper-audio');
const {appendBuffer, isBrowser, throttleTimeout} = require('koush-utils/lib/base/util');

function resampleAudio(audioBuffer, outputSampleRate, offset, sampleLength) {
  var floatSample = new Float32Array(sampleLength);
  var outputSampleLength = Math.round(outputSampleRate * sampleLength / audioBuffer.sampleRate);
  var offlineContext = new OfflineAudioContext(audioBuffer.numberOfChannels, outputSampleLength, outputSampleRate);
  var buffer = offlineContext.createBuffer(audioBuffer.numberOfChannels, sampleLength, audioBuffer.sampleRate);

  for (var i = 0; i < audioBuffer.numberOfChannels; i++) {
    audioBuffer.copyFromChannel(floatSample, 0, offset);
    buffer.copyToChannel(floatSample, i);
  }

  var source = offlineContext.createBufferSource();
  source.buffer = buffer;
  source.connect(offlineContext.destination);
  source.start();

  return offlineContext.startRendering();
}

function AudioQueue(context) {
  this.context = context;
  this.numSamples = 1024;
  this.numSamplesDiv2 = this.numSamples / 2;
  this.numSamplesDiv4 = this.numSamples / 4;
  this.cleanup = this.numSamples / 2;
  this.samples = new Array(this.numSamples);
  this.lastIndex = 0;
}

AudioQueue.prototype.verify = function(sample) {
  if (!sample)
    return;
  if (sample[0] != 0xff || (sample[1] & 0xf0) != 0xf0) {
    this.malformed = throttleTimeout(this.malformed, null, 5000, function() {
      console.error('got a malformed adts sample?');
    })
  }
}

AudioQueue.prototype.stop = function() {
  if (this.source != null)
    this.source.stop();
  if (this.pepperAudio)
    this.pepperAudio.stop();
  this.pepperAudio = null;
  this.source = null;
}

AudioQueue.prototype.queuePCMData = function(index, floatSample0, floatSample1) {
  this.audioBuffer.copyToChannel(floatSample0, 0, index * this.sampleLength);
  this.audioBuffer.copyToChannel(floatSample1, 1, index * this.sampleLength);

  if (this.samplesDecoded == 10)
    this.source.start();
  this.samplesDecoded++;
}

AudioQueue.prototype.decode = function(id, debugInfo) {
  var index = (id + this.numSamples) % this.numSamples;
  var useLegacy = isBrowser();

  var adts = this.samples[index];

  if (adts == null) {
    // if we don't have a sample, we gotta clear out the looping buffer
    // or we end up getting audio jitters (old valid packets) on new missing packets.

    // maybe move the silencing to when the timestamps roll over?
    if (this.silence) {
      this.audioBuffer.copyToChannel(this.silence, 0, index * this.sampleLength);
      this.audioBuffer.copyToChannel(this.silence, 1, index * this.sampleLength);
    }
    return;
  }

  var before = (id - 1 + this.numSamples) % this.numSamples;
  var after = (id + 1) % this.numSamples;

  // decodes seem to fail with less than 3 samples? i don't understand.
  if (!useLegacy) {
    before = this.samples[before];
    after = this.samples[after];
    if (!before || !after) {
      // can't get a seamless decode here.
      return;
    }
  }
  else {
    before = this.samples[before] || adts;
    after = this.samples[after] || adts;
}

  // before = this.samples[before];
  // after = this.samples[after];

  var combined;
  var samples = 1;
  if (before) {
    samples++;
    combined = appendBuffer(before, adts);
  }
  else {
    combined = adts;
  }

  if (after) {
    samples++;
    combined = appendBuffer(combined, after);
  }

  // this.verify(before);
  // this.verify(adts);
  // this.verify(after);


  // var start = Date.now();
  this.context.decodeAudioData(combined.sliceArrayBuffer(), function(decoded) {
    // console.log('decode', Date.now() - start);

    if (!useLegacy && !this.pepperAudio) {
      console.log('pepper audio plugin created');
      this.pepperAudio = new PepperAudio();
    }

    if (useLegacy && !this.source) {
      this.sampleLength = decoded.length / samples;

      var sampleRate = decoded.sampleRate;
      console.log('audio sample rate', sampleRate);

      // seems xiaomi phones advertise 48000 during the protocol buffers sessions,
      // then it sends adts that says it is 192000 in the header
      // but then the actual sample rate is 48000...
      if (this.sampleRate && sampleRate != this.sampleRate) {
        console.log('Decoded sampleRate does not match', sampleRate, this.sampleRate);
        sampleRate = this.sampleRate;
      }

      // sanity check this as well
      var numberOfChannels = decoded.numberOfChannels;
      if (this.numberOfChannels && decoded.numberOfChannels != this.numberOfChannels) {
        console.log('Decoded numberOfChannels does not match', numberOfChannels, this.numberOfChannels);
        numberOfChannels = this.numberOfChannels;
      }

      var silence = new AudioBuffer({
        length: this.sampleLength,
        numberOfChannels: numberOfChannels,
        sampleRate: sampleRate,
      })
      this.silence = new Float32Array(this.sampleLength);
      silence.copyFromChannel(this.silence, 0);

      this.audioBuffer = new AudioBuffer({
        length: this.sampleLength * this.numSamples,
        numberOfChannels: numberOfChannels,
        sampleRate: sampleRate,
      })

      this.source = this.context.createBufferSource();
      this.source.buffer = this.audioBuffer;
      this.source.loop = true;
      this.source.connect(this.context.destination);

      this.samplesDecoded = 0;
    }

    if (useLegacy && decoded.length != this.sampleLength * samples) {
      console.error('unexpected decoded length');
      return;
    }

    // Need to use per channel allocated samples, the copyFrom and copyTo seem to be async... or something?
    // there is a lot of jittering as what appears to be reads happening during writes. By throwing these
    // away, it stopped.

    // grab the portion we are interested in
    var sampleLength = decoded.length / samples;

    if (false && decoded.sampleRate != this.context.sampleRate) {
      resampleAudio(decoded, decoded.sampleRate, 192000, before ? sampleLength : 0, sampleLength)
      .then(function(resampledBuffer) {
        this.queuePCMData(index, resampledBuffer.getChannelData(0), resampledBuffer.getChannelData(1))
      }.bind(this))
      return;
    }

    var floatSample = new Float32Array(sampleLength);
    var floatSample1 = new Float32Array(sampleLength);
    decoded.copyFromChannel(floatSample, 0, before ? sampleLength : 0);
    decoded.copyFromChannel(floatSample1, 1, before ? sampleLength : 0);

    if (useLegacy) {
      this.queuePCMData(index, floatSample, floatSample1);
    }
    else {
      this.pepperAudio.queueAudio(id, floatSample, floatSample1);
    }

  }.bind(this),
  function(error) {
    if (this.onError)
      this.onError(debugInfo, samples, combined, error);
  }.bind(this))
  // no longer needed cause decoding stops after the first error anyways, keeping this for posterity though
  // .catch(function() {
  //   // this needs to be here or we get some spam in the error log
  // });

}

AudioQueue.prototype.queue = function(id, adts, debugInfo) {
  var index = id % this.numSamples;

  // divide the looping buffer into 4 parts, and when we start
  // receiving new packets for a quadrant, clear out the opposite quadrant
  if (index >= this.cleanup && index < this.cleanup + this.numSamplesDiv4) {
    var start = (this.cleanup + this.numSamplesDiv2) % this.numSamples;
    var end = start + this.numSamplesDiv4;
    for (var i = start; i < end; i++) {
      this.samples[i] = null;
    }
    this.cleanup = (this.cleanup + this.numSamplesDiv4) % this.numSamples;
  }

  this.samples[index] = adts;

  // whenever we get a new sample, we need to redecode everything around it to remove audio seams (or we get jitters)

  // var before = (index - 1 + this.numSamples) % this.numSamples;
  // var after = (index + 1) % this.numSamples;

  this.decode(id - 1, debugInfo);
  this.decode(id, debugInfo);
  this.decode(id + 1, debugInfo);
}

module.exports = AudioQueue;