import Worker from './H264NALDecoder.worker'
import CanvasRenderer from './CanvasRenderer'
import TinyH264Decoder from 'tinyh264/es/TinyH264Decoder'
import TinyH264 from 'tinyh264/es//TinyH264'
import tinyH264WasmURL from 'tinyh264/es//TinyH264.wasm.asset'

const assets = {
  'TinyH264.wasm': tinyH264WasmURL
}
const tinyH264 = TinyH264({ locateFile: path => assets[path] })

let videoStreamIdCounter = 1

class TinyDecoder {
  constructor(listener, encodeWidth, encodeHeight) {
    this.queue = [];
    this.videoStreamId = videoStreamIdCounter++;
    this.pendingDecodes = 0;

    // off scren canvas is implemented but is super janky.
    // must keep in sync with useCanvasWorker in the h264 worker.
    this.useOffscreenCanvas = true;
    // useOffscreenCanvas requires worker.
    // useWorker is preferred because running in the browser thread is janky.
    this.useWorker = true || this.useOffscreenCanvas;

    if (this.useWorker) {
      this.tinyH264Worker = new Worker()
      this.tinyH264Worker.addEventListener('message', (e) => {
        const message = /** @type {{type:string, width:number, height:number, data:ArrayBuffer, renderStateId:number}} */e.data
        switch (message.type) {
          case 'pictureReady':
            this.onPictureReady(message)
            break
          case 'decoded':
            this.notifyDecoded();
            break;
          case 'decoderReady':
            this.notifyReady(listener);
            break
        }
      })
    }
    else {
      this.decoder = new TinyH264Decoder(tinyH264, (output, width, height) => {
        this.canvasRenderer.onPicture(output, width, height)
      });
      setTimeout(() => this.notifyReady(listener), 0);
    }
  }

  notifyDecoded() {
    this.pendingDecodes--;
    this.decodeNext();
  }

  notifyReady(listener) {
    this.initWebGLCanvas(listener)
    if (this.onReady)
      this.onReady();
    else
      console.error('no listener for decoder-ready')
  }

  initWebGLCanvas (listener) {
    while (listener.firstChild) {
      listener.removeChild(listener.firstChild);
    }
    let canvas = document.createElement('canvas')
    canvas.style.width ='100%';
    canvas.style.height='100%';
    listener.append(canvas)

    if (this.useOffscreenCanvas && canvas.transferControlToOffscreen) {
      var offscreen = canvas.transferControlToOffscreen();

      this.tinyH264Worker.postMessage({
        type: 'canvas',
        canvas: offscreen,
        renderStateId: this.videoStreamId
      }, [offscreen]);
    }
    else {
      this.canvasRenderer = new CanvasRenderer(canvas);
    }
  }


  /**
   * @param {{width:number, height:number, data: ArrayBuffer}}message
   */
  onPictureReady (message) {
    const { width, height, data } = message
    this.canvasRenderer.onPicture(new Uint8Array(data), width, height)
  }

  release () {
    if (this.tinyH264Worker) {
      tinyH264Worker.postMessage({ type: 'release', renderStateId: this.videoStreamId })
      tinyH264Worker = null
    }
  }

  /**
   * @param {Uint8Array} h264Nal
   */
  enqueueDecode(h264Nal) {
    if (this.tinyH264Worker) {
      this.tinyH264Worker.postMessage({
        type: 'decode',
        data: h264Nal.buffer,
        offset: h264Nal.byteOffset,
        length: h264Nal.byteLength,
        renderStateId: this.videoStreamId
      }, [h264Nal.buffer])
    }
    else {
      this.decoder.decode(h264Nal);
      this.notifyDecoded();
    }
  }

  decodeNext () {
    if (this.queue.length == 0) {
      return;
    }

    if (this.pendingDecodes > 10) {
      return;
    }
    this.pendingDecodes++;

    const packet = this.queue.shift();
    const nextFrame = packet.video;
    this.enqueueDecode(nextFrame)
  }

  decode(video, presentationTimeMs, syncFrame) {
    if (syncFrame) {
      this.queue = [];
    }

    this.queue.push({
      video,
      presentationTimeMs,
    });

    this.decodeNext();
  }
}

export default TinyDecoder;