function sendEvent(e) {
  if (!inputWebSocket)
    return;
  if (e.clientX) {
    e.downDelta = Date.now() - downTime;
    if (!listenerDiv)
      listenerDiv = $('#listener');
    e.clientX = e.clientX / listenerDiv.width() * screenWidth;
    if (e.clientX > screenWidth || e.clientX < 0)
      return;
  }
  if (e.clientY) {
    if (!listenerDiv)
      listenerDiv = $('#mirror');
    e.clientY = e.clientY / listenerDiv.height() * screenHeight;
    if (e.clientY > screenHeight || e.clientY < 0)
      return;
  }
  inputWebSocket.send(JSON.stringify(e));
}

function interpretEvent(e, source) {
  if (!e)
    return;

  if (e.constructor.name == 'String') {
    if (e.startsWith('KEYCODE_')) {
      sendEvent({
        type: 'keyevent',
        keyevent: e
      })
      return true;
    }
    else {
      switch (e) {
        case 'VYSOR_SHOW_TITLE_BAR':
          delayFadeHead();
          break;
        case 'VYSOR_ENTER_FULLSCREEN':
          setFullscreen(true);
          break;
        case 'VYSOR_EXIT_FULLSCREEN':
          setFullscreen(false);
          break;
        case 'VYSOR_RECORD_SCREEN':
          if (!isBrowser())
            toggleRecord(source);
          break;
        case 'VYSOR_SCREENSHOT':
          if (!isBrowser())
            chrome.browser.openTab({url: 'http://127.0.0.1:' + window.httpPort + '/device/' + (device.id || device.serialno) + '/screenshot-' + Date.now() + '.jpg'});
          break;
        case 'VYSOR_OPEN_DEVICE_SETTINGS':
          showSettings();
          if (!isBrowser())
            openList();
          break;
        case 'VYSOR_ROTATE_SCREEN':
          sendEvent({
            type: 'rotate'
          })
          break;
        default:
          return;
      }
      return true;
    }
  }
  else if (e.constructor.name == 'Number') {
    sendEvent({
      type: 'keycode',
      keycode: e
    })
    return true;
  }
  else {
    if (e.keyevent) {
      sendEvent({
        type: 'keyevent',
        keyevent: e.keyevent,
        shift: (e.shiftKey && source && source.shiftKey) ? true : false
      })
      return true;
    }
    else if (e.keycode) {
      sendEvent({
        type: 'keycode',
        keycode: e.keycode,
        shift: (e.shiftKey && source && source.shiftKey) ? true : false
      })
      return true;
    }
  }
}

function delayFadeHead() {
  if (vysorMode == 'inkwire') {
    $('.head').hide();
    return;
  }
  if (titleBarHeight) {
    clearTimeout(headFader);
    $('.head').fadeTo(0, 1);
    return;
  }
  $('.head').fadeTo(0, .8);
  clearTimeout(headFader);
  headFader = setTimeout(function() {
    $('.head').fadeOut();
  }, 2000);
}

