var resizeTimeout;
$(window).resize(function() {
  if (!screenHeight || !screenWidth)
    return;

  clearTimeout(resizeTimeout);
  resizeTimeout = setTimeout(function() {
    if (!isFreeSize()) {
      // always read inner*, but preferably set innerBounds
      // if (window.chrome && window.chrome.app && window.chrome.app.window)
      //   iw = chrome.app.window.current().innerBounds.width;
      var iw = innerWidth;
      var newScreenHeight = iw * (screenHeight / screenWidth);

      rectWidth = iw;
      rectHeight = newScreenHeight;

      resizeWindow();
    }
    else {
      sendEvent({
        type: 'displaySettings',
        size: innerWidth + 'x' + (innerHeight - titleBarHeight - bottomBarHeight),
        density: deviceSettings.displaySettings.density,
      })
    }
  }, 500)
})

function enforceAspectRatio(ow, oh) {
  if (isFreeSize()) {
    rectWidth = screenWidth;
    rectHeight = screenHeight;
  }
  else {
    if (window.chrome && window.chrome.app && window.chrome.app.window && isFullscreen()) {
      rectWidth = screen.availWidth;
      rectHeight = screen.availHeight;
    }

    if (oh == screenWidth && ow == screenHeight) {
      // orientation change
      var tmp = rectWidth;
      rectWidth = rectHeight;
      rectHeight = tmp;
    }
    else {
      var iw;
      if (screenWidth > screenHeight)
        iw = Math.max(innerWidth, innerHeight);
      else
        iw = Math.min(innerWidth, innerHeight);
      // always read inner*, but preferably set innerBounds
      // if (window.chrome && window.chrome.app && window.chrome.app.window)
      //   iw = chrome.app.window.current().innerBounds.width;

      var newScreenHeight = iw * (screenHeight / screenWidth);
      rectWidth = iw;
      rectHeight = newScreenHeight;
    }
  }

  resizeWindow();
}

function isFullscreen() {
  return document.webkitFullscreenElement || document.msFullscreenElement || document.mozFullscreenElement || document.fullscreenElement;
}

function isFreeSize() {
  return deviceSettings && deviceSettings.displaySettings && deviceSettings.displaySettings.freeSize;
}

function resizeWindow() {
  // always read inner*, but preferably set innerBounds
  var iw = innerWidth;
  var ih = innerHeight;

  var maxWidth = screen.availWidth;
  var maxHeight = screen.availHeight;

  if (isFullscreen() || isFreeSize() || isBrowser()) {
    maxWidth = iw;
    maxHeight = ih;
  }

  if (rectWidth > maxWidth) {
    rectWidth = maxWidth;
    rectHeight = screenHeight / screenWidth * rectWidth;
  }
  // do i need this? we are setting the innerBounds/contentSize
  // var windowDecorHeight = outerHeight - innerHeight;
  var windowDecorHeight = 0;
  if (rectHeight + bottomBarHeight + titleBarHeight + windowDecorHeight > maxHeight) {
    rectHeight = maxHeight - bottomBarHeight - titleBarHeight - windowDecorHeight;
    rectWidth = screenWidth / screenHeight * rectHeight;
  }

  // rectWidth = Math.round(rectWidth);
  // rectHeight = Math.round(rectHeight);

  // may not have values yet
  if (!rectWidth || !rectHeight)
    return;

  // prevent resize noise by allowing some degree of error
  var rw = Math.round(rectWidth);
  var rh = Math.round(rectHeight + bottomBarHeight + titleBarHeight);
  if (!isFullscreen() && !isFreeSize()) {
    if (ignoreNextResize) {
      ignoreNextResize = false;
    }
    else {
      ignoreNextResize = true;
      if (isElectron()) {
        if (Math.abs(innerWidth - rw) >= 2 || Math.abs(innerHeight - rh) >= 2)
          chrome.app.window.current().w.setContentSize(rw, rh)
      }
      else if (window.chrome && window.chrome.app && window.chrome.app.window) {
        if (Math.abs(innerWidth - rw) >= 2)
          chrome.app.window.current().innerBounds.width = rw;
        if (Math.abs(innerHeight - rh) >= 2)
          chrome.app.window.current().innerBounds.height = rh;
      }
    }
  }

  var l = (iw - rectWidth) / 2;
  var c = $('#mirror, #listener');
  c.css('left', l)
  c.css('top', (ih - rectHeight - bottomBarHeight + titleBarHeight) / 2);
  $('.foot').width(rectWidth);
  $('.foot').css('left', l);
  $('.overlay').width(rectWidth);
  $('.overlay').css('left', l);

  c = $('#mirror, embed, #listener');
  c.width(Math.round(rectWidth));
  c.height(Math.round(rectHeight));

  $('#dead, #drag, #reconnect').width(rectWidth);
  $('#dead, #drag, #reconnect').height(rectHeight + bottomBarHeight + titleBarHeight);
}

function trackUsage(elapsed) {
  if (!window.chrome || !window.chrome.storage)
    return;
  chrome.storage.local.get(['vysorUsage', 'vysorDailyUsage', 'lastDailyReport'], function(d) {
    var vysorUsage = d.vysorUsage;
    var lastDailyReport = d.lastDailyReport;
    var vysorDailyUsage = d.vysorDailyUsage;
    var now = Date.now();
    if (!vysorUsage)
      vysorUsage = 0;
    if (!vysorDailyUsage)
      vysorDailyUsage = 0;

    if (!lastDailyReport) {
      chrome.storage.local.set({
        'lastDailyReport': now
      })
    }
    else {
      if (now > lastDailyReport + 24 * 60 * 60 * 1000) {
        // send analytics
        var hoursUsed = vysorDailyUsage / (60 * 60 * 1000);
        // half hour
        hoursUsed = Math.round(hoursUsed * 2) / 2;
        if (hoursUsed <= 24 && hoursUsed >= 0) {
          tracker.sendEvent('daily-usage', hoursUsed.toString());
          tracker.sendEvent('daily-usage-licensed', _lm._il);
        }
        // reset
        vysorDailyUsage = 0;
        chrome.storage.local.set({
          'lastDailyReport': now
        })
      }
    }

    var beforeVysorUsage = vysorUsage;
    vysorUsage += elapsed;

    if (beforeVysorUsage < 2 * 60 * 60 * 1000 && vysorUsage > 2 * 60 * 60 * 1000) {
      if (!window._lm || !window._lm._il) {
        recommendProOnce();
      }
    }
    else if (vysorUsage > 3 * 60 * 60 * 1000) {
      recommendRateOnce();
    }

    vysorDailyUsage += elapsed;
    chrome.storage.local.set({
      'vysorUsage': vysorUsage,
      'vysorDailyUsage': vysorDailyUsage
    })
  })
}

function setFullscreen(fullscreen) {
  if (!fullscreen) {
    if (document.exitFullscreen)
      document.exitFullscreen();
    else if (document.webkitExitFullscreen)
      document.webkitExitFullscreen();
    else if (document.mozExitFullscreen)
      document.mozExitFullscreen();
    else if (document.mskitExitFullscreen)
      document.mskitExitFullscreen();
  }
  else {
    if (document.documentElement.requestFullscreen)
      document.documentElement.requestFullscreen();
    else if (document.documentElement.webkitRequestFullscreen)
      document.documentElement.webkitRequestFullscreen();
    else if (document.documentElement.msRequestFullscreen)
      document.documentElement.msRequestFullscreen();
    else if (document.documentElement.mozRequestFullScreen)
      document.documentElement.mozRequestFullScreen();
  }
};

function setupTitleBar() {
  $('.topbuttonbar>.btn-toolbar').empty();
  $('.head').attr('style', vysorSettings.titleBar.style)
  var buttonGroups = vysorSettings.titleBar.buttonGroups;

  (function() {
    if (isBrowser())
      return;
    // audio
    var outer = $("<span id='headphones'></span>");
    outer.attr('class', 'btn-group btn-group-xs');
    outer.attr('role', 'group');

    var el = $("<a></a>");
    el.attr('class', 'btn btn-default fa fa-headphones');
    el.attr('title', 'Mute Phone and Mirror Audio');
    el.click(libind('Audio Mirroring', function() {
      ensureAudioContext();
      maybeConnectUDP();
    }));

    outer.append(el);

    $('.topbuttonbar>.btn-toolbar').append(outer);
  })();

  $(buttonGroups).each(function(i, buttonGroup) {
    var outer = $("<span></span>");
    outer.attr('class', 'btn-group btn-group-xs');
    outer.attr('role', 'group');

    $(buttonGroup).each(function(j, button) {
      var el = $("<a></a>");
      el.attr('class', 'btn btn-default fa ' + button.class);
      el.attr('title', button.title);
      el.click(function(e) {
        interpretEvent(button.event, e);
      })
      outer.append(el);
    });

    $('.topbuttonbar>.btn-toolbar').append(outer);
  })
}

function setupNavigationBar() {
  $('.foot-row').empty();
  $('.foot-row').attr('style', vysorSettings.navigationBar.style)
  var buttons = vysorSettings.navigationBar.buttons;
  var width = Math.round(100 / buttons.length) + '%';
  $(buttons).each(function(i, button) {
    var el = $("<span></span>");
    el.attr('class', 'fa fa-lg ' + button.class);
    el.attr('title', button.title);
    el.width(width);
    el.click(function() {
      interpretEvent(button.event);
    })
    $('.foot-row').append(el);
  })
}

