sharedGlobals.connectionReady = function() {
  console.log('connectionReady')
  $('#loading').fadeIn();
  $('#dead, #reconnect').fadeOut();
  delayFadeHead();

  if (!device)
    console.error('device not defined on window object?');

  if (window.chrome && window.chrome.storage && window.chrome.storage.local) {
    chrome.storage.local.get('vysorSettings', function(data) {
      vysorSettings = {};
      // populate defaults, and overwrite with user defaults.
      // this will guarantee missing keys on updates.
      Object.assign(vysorSettings, vysorDefaultSettings, data.vysorSettings);
      var deviceSpecific = (vysorSettings.devices && vysorSettings.devices[device.id || device.serialno]) || {};
      // possibly overwrite nav bar and title bar
      vysorSettings.navigationBar = deviceSpecific.navigationBar || vysorSettings.navigationBar;
      vysorSettings.titleBar = deviceSpecific.titleBar || vysorSettings.titleBar;
      // merge keys and mouse
      Object.assign(vysorSettings.keydown, deviceSpecific.keydown);
      Object.assign(vysorSettings.mousedown, deviceSpecific.mousedown);
      setupNavigationBar();
      setupTitleBar();
    })
  }

  var sizeThrottle;
  function handleData(json) {
    if (json.type == 'displaySize') {
      var ow = screenWidth;
      var oh = screenHeight;
      screenWidth = json.screenWidth;
      screenHeight = json.screenHeight;

      enforceAspectRatio(ow, oh);
    }
    else if (json.type == 'encodeSize') {
      encodeWidth = json.encodeWidth;
      encodeHeight = json.encodeHeight;

      if (!h264Socket) {
        connect264();
        return;
      }

      sizeThrottle = throttleTimeout(sizeThrottle, null, 1000, function() {
        h264Socket.onClose = null;
        h264Socket.destroy();
        h264Socket = null;
        connect264();
      });
    }
    else if (json.type == 'clip') {
      copyTextToClipboard(json.clip);
    }
    else if (json.type == 'password') {
      // ignore
    }
    else if (json.type == 'recording') {
      var recording = json.recording.replace('/sdcard/vysor/', '');
      chrome.browser.openTab({url: 'http://127.0.0.1:' + httpPort + '/device/' + (device.id || device.serialno) + '/sdcard-vysor/' + recording});
    }
    else {
      console.log('unknown', json);
    }
  }

  var attemptCount = 0;
  var maxRetries = 20;
  function reattempt() {
    console.log('input websocket failed, retrying. attempt ' + attemptCount + ' out of ' + maxRetries);
    setTimeout(attemptConnection, 500);
  }

  function attemptConnection() {
    // don't do this. websocket disappearing causes the vysor daemon to terminate.
    // if (inputWebSocket)
    //   inputWebSocket.close();
    // inputWebSocket = null;

    attemptCount++;
    if (attemptCount == maxRetries) {
      withSocket();
      return;
    }

    createWebSocket(function(ws) {
      if (!ws) {
        reattempt();
        return;
      }
      inputWebSocket = ws;
      // force a reconnect with new transport.
      if (h264Socket) {
        h264Socket.onClose = null;
        h264Socket.destroy();
        h264Socket = null;
      }
      inputWebSocket.onopen = function() {
        console.log('input websocket opened');

        if (!isBrowser()) {
          Adb.reverse({
            serialno: device.serialno,
            from: 'tcp:8010',
            to: 'tcp:' + castServer.getPort(),
          }, function(result) {
            console.log('audio listener', result);
          })
        }

        inputWebSocket.onerror = null;
        var displaySettings = deviceSettings.displaySettings || {};
        var size = displaySettings.freeSize ? innerWidth + 'x' + (innerHeight - bottomBarHeight - titleBarHeight) : displaySettings.size == 'reset' ? null : displaySettings.size;
        var density = displaySettings.density == 'reset' ? null : displaySettings.density;
        // send password, and initial display settings.
        // password event also wakes phone up
        sendEvent({
          type: 'password',
          password: password,
          resolution: resolutions[deviceSettings.resolution],
          size: size,
          density: density,
          dimDisplay: deviceSettings.dimDisplay,
        })
      }
      inputWebSocket.onerror = reattempt;
      inputWebSocket.onmessage = function (event) {
        console.log('event from phone', event.data);
        var json = JSON.parse(event.data);
        handleData(json);
      }
    });
  }

  if (!window.adbSocketFactory) {
    if (adbSocketFactoryFactory.type == 'adb-client') {
      adbSocketFactory = Adb.createSocketFactory(adbSocketFactoryFactory.arguments.serialno);
    }
    else {
      console.error('unknown adb socket factory factory', adbSocketFactoryFactory.type)
    }
  }

  var createWebSocket = function(cb) {
    adbSocketFactory.newSocket('tcp:53518', function(ws) {
      if (!ws) {
        cb();
        return;
      }
      var queued;
      var writeCallback = function() {
        if (!queued)
          return;
        var next;
        if (queued.length)
          next = queued.shift();
        else
          queued = null;
        if (next)
          writeLine(ws, next, writeCallback);
      }
      ws.close = ws.destroy.bind(ws);
      ws.send = function(json) {
        if (queued) {
          queued.push(json);
          return;
        }
        queued = [];
        writeLine(ws, json, writeCallback);
      }.bind(ws);
      cb(ws);
      var hasConnected = false;
      ws.onClose = function() {
        if (!hasConnected) {
          if (ws.onerror)
            ws.onerror();
        }
      }
      function onblob(s) {
        if (!hasConnected) {
          hasConnected = true;
          if (ws.onopen)
            ws.onopen();
        }
        if (ws.onmessage)
          ws.onmessage({data: s})
        readLine(ws, onblob);
      }
      readLine(ws, onblob);
    })
  };

  console.log('init');

  function socketFail() {
    if (window.tryReconnect) {
      $('#reconnect').fadeIn();
    }
    else {
      $('#dead').fadeIn();
    }
    $('#listener').empty();
  }

  function withSocket(socket) {
    console.log('received socket', socket);

    if (h264Socket) {
      h264Socket.onClose = null;
      h264Socket.destroy();
      h264Socket = null;
    }

    h264Socket = socket;

    $('.overlay').fadeOut();
    if (!socket) {
      socketFail();
      return;
    }

    socket.name = 'h264';

    socket.onClose = function() {
      try {
        socketFail();
      }
      catch (e) {
        console.log('eating error that occurs with jquery if window is being closed...');
      }
      console.log('closing input ')
      if (inputWebSocket)
        inputWebSocket.close();
      inputWebSocket = null;
      if (h264Socket == socket)
        h264Socket = null;
    }

    // var decoder = new BroadwayDecoder();
    var defaultDecoder = isBrowser() ? 3 : 0; // tinyh264 or pnacl software
    var decoderSetting = decoders[deviceSettings.decoder || defaultDecoder];
    console.log('decoder: ' + decoderSetting);
    if (isBrowser() || decoderSetting == 'tinyh264') {
      var decoder = new TinyDecoder($('#listener')[0], encodeWidth, encodeHeight);
    }
    else {
      var decoder = new PepperDecoder(encodeWidth, encodeHeight, decoderSetting);
    }

    var isSps = true;
    var frameTracker;
    function withPayload(data) {
      var video = data.subarray(8);
      var dv = new DataView(data.buffer, data.byteOffset, 8);
      var presentationTimeMs = dv.getUint32(0, true);
      var syncFrame = dv.getUint32(4, true) != 0;

      if (flash) {
        if (socket == h264Socket) {
          if (isSps)
            flash.addVideoHeader(video);
          else if (!needsRestartForVideoDump)
            flash.addVideoFrame(video, presentationTimeMs, syncFrame);
        }
      }

      if (isSps) {
        needsRestartForVideoDump = false;
        isSps = false;
        // if (video[5] != 66 && video.byteLength >= 6)
        //   showNotification('Your Android needs to send H264 Baseline by default. You may get this error and see a black screen if running a custom ROM.')
      }

      decoder.decode(video, presentationTimeMs, syncFrame)

      // log every minute
      var now = Date.now();
      if (!frameTracker) {
        frameTracker = now;
        tracker.sendEvent('view-device');
      }
      else {
        var elapsed = now - frameTracker;
        if (elapsed > 60 * 1000) {
          frameTracker = now;
          trackUsage(elapsed);
        }
      }
    }

    var payloadReader = function(data) {
      withPayload(data);

      readHeader();
    }

    var headerReader = function(data) {
      if (data.byteLength != 4) {
        throw new Error("WTF");
      }
      var lenBuf = new DataView(data.buffer, data.byteOffset, 4);
      var len = lenBuf.getInt32(0, true);
      socket.read(len, payloadReader);
    }

    var readHeader = function() {
      socket.read(4, headerReader);
    }

    function startReadLoop() {
      console.log('starting read loop');
      var firstRead = setTimeout(function() {
        showNotification("Your Android screen is unavailable right now. This can sometimes be resolved by rebooting your Android.");
      }, 5000);

      // this has a timer
      startAdViewer();

      readLenPackets(socket, function(data) {
        if (firstRead) {
          clearTimeout(firstRead);
          firstRead = undefined;
          recommendHighQualityOnce();

          if (window.chrome && window.chrome.storage) {
            sendEvent({
              type: 'bitrate',
              bitrate: bitrates[deviceSettings.bitrate]
            })
          }
        }

        for (var packet of data) {
          withPayload(packet);
        }
      }, true);
    }

    var syncFrameThrottle;
    function decoderFallingBehind() {
      syncFrameThrottle = throttleTimeout(syncFrameThrottle, null, 1000, function() {
        console.log('requesting sync frame');
        sendEvent({type: "sync-frame"});
      });
    }

    decoder.onDecoderFallingBehind = decoderFallingBehind;
    decoder.onReady = startReadLoop;
    resizeWindow();
  }

  function connect264() {
    adbSocketFactory.newSocket('tcp:53517', function(socket) {
      if (!socket) {
        withSocket();
        return;
      }

      writeLine(socket, password, function() {
        withSocket(socket);
      });
    });
  }

  loadDeviceSettings(device.id || device.serialno, function(d) {
    deviceSettings = d;

    attemptConnection();

    // sanitize the settings for the license manager
    if (!window._lm || !window._lm._il) {
      deviceSettings.bitrate = 0;
      deviceSettings.resolution = 0;
      deviceSettings.alwaysOnTop = false;
    }
    applyDeviceSettings(deviceSettings);
    useLicenseCheck = true;
    resizeWindow();
    $('.title, title').text(deviceSettings.friendlyName || device.name);
  });
}

