var Buffer = require('buffer').Buffer;

var hasChromeSockets = false;
try {
  if (chrome && chrome.sockets) {
    chrome.sockets.tcp.onReceive.addListener(function(resultData) {
      var socket = Socket.readers[resultData.socketId];
      // could be owned by another process or something...
      if (socket == null)
        return;
      // console.log('got data: ' + ab2str(resultData.data));
      socket.dataReceived(new Uint8Array(resultData.data));
    });
    chrome.sockets.tcp.onReceiveError.addListener(function(resultData) {
      var socket = Socket.readers[resultData.socketId];
      // console.log('socket died');
      // could be owned by another process or something...
      if (socket == null)
        return;
      socket.destroy();
      socket.dataReceived(null);
    });
  
    chrome.sockets.tcpServer.onAccept.addListener(function(acceptInfo) {
      chrome.sockets.tcp.setPaused(acceptInfo.clientSocketId, false);
      var listener = Server.listeners[acceptInfo.socketId];
      if (listener == null)
        return;
      listener(new Socket({socketId: acceptInfo.clientSocketId}));
    });

    hasChromeSockets = true;
  }
}
catch (ignored) {
  console.log('chrome app socket initialization failed')
  console.warn(ignored);
}


function Socket(options, cb) {
  if (options.socketId) {
    this.socketId = options.socketId;
    Socket.readers[this.socketId] = this;
  }
  else if (hasChromeSockets) {
    chrome.sockets.tcp.create(function(createInfo) {
      this.socketId = createInfo.socketId;
      chrome.sockets.tcp.connect(this.socketId, options.host, options.port, function(result) {
        if (!result) {
          Socket.readers[createInfo.socketId] = this;
          cb(this);
        }
        else {
          // must read this to prevent log spam
          chrome.runtime.lastError;
          this.destroy();
          cb(null);
        }
      }.bind(this));
    }.bind(this));
  }
  else {
    // electron
    var connected;
    if (!options.ns) {
      this.ns = new __non_webpack_require__('net').Socket();
      this.ns.connect({
        port: options.port,
        host: options.host,
      }, function() {
        connected = true;
        cb(this);
      }.bind(this))
    }
    else {
      this.ns = options.ns
      connected = true;
    }
    this.ns.on('close', function() {
      this.destroy();
      if (!connected)
        cb(null);
    }.bind(this))
    this.ns.on('data', function(d) {
      this.dataReceived(d);
    }.bind(this));
  }
}
Socket.readers = {};


Socket.connect = function(options, cb) {
  return new Socket(options, cb);
}

Socket.pump = function(s1, s2, cb) {
  if (!s1 || !s2) {
    console.error('Socket.pump called with null socket', s1, s2);
    cb();
    return;
  }

  var writeDone = function() {
    s1.read(reader);
  }.bind(s1);
  var reader = function(data) {
    var buffer = data.buffer;
    if (data.byteOffset || data.length != buffer.byteLength) {
      buffer = buffer.slice(data.byteOffset, data.byteOffset + data.length);
    }
    s2.write(buffer, writeDone);
  }.bind(s2);
  s1.read(reader);
  s1.onClose = cb;
}

Socket.stream = function(s1, s2, cb) {
  Socket.pump(s1, s2, function() {
    if (s2)
      s2.destroy();
    if (cb) {
      var tmp = cb;
      cb = null;
      tmp();
    }
  });
  Socket.pump(s2, s1, function() {
    if (s1)
      s1.destroy();
    if (cb) {
      var tmp = cb;
      cb = null;
      tmp();
    }
  });
}

Socket.eat = function(s) {
  function reader() {
    s.read(reader);
  }
  reader();
}

Socket.prototype.read = function() {
  if (this.pendingCallback) {
    throw new Error("double callback");
  }

  // see if we need to alert a close event
  if (this.closed && !this.pending) {
    var cb = this.onClose;
    if (cb) {
      delete this.onClose;
      cb();
    }
    return;
  }

  var argc = 0;
  if (arguments[argc].constructor.name == 'Number') {
    this.pendingLength = arguments[argc++];
  }
  else {
    this.pendingLength = 0;
  }
  var cb = arguments[argc];

  // see if we can fulfill this request
  if (!this.pending || this.paused) {
    // nope
    this.pendingCallback = cb;
    return;
  }

  if (!this.pendingLength) {
    // take it all!
    this.pendingLength = this.buffered();
  }
  else if (this.pendingLength > this.buffered()) {
    this.pendingCallback = cb;
    return;
  }

  var data;
  var totalRead = 0;
  while (totalRead < this.pendingLength) {
    var buf = this.pending.shift();
    this.bufferedLength -= buf.length;
    // allow gc
    if (!this.pending.length)
      delete this.pending;
    var add = buf;
    var need = Math.min(add.byteLength, this.pendingLength - totalRead);
    if (need != add.byteLength) {
      var part = add.subarray(0, need);
      var leftover = add.subarray(need);
      this.unshift(leftover);
      add = part.slice();
    }
    if (!data && add.byteLength != this.pendingLength)
      data = new Uint8Array(this.pendingLength);
    if (data) {
      // append
      data.set(add, totalRead);
    }
    else {
      // this is exactly what we need, so just use this
      data = add;
    }
    totalRead += add.byteLength;
  }
  cb(data);
}

Socket.prototype.write = function(data, cb) {
  // console.log('writeing: ' + ab2str(data));
  if (this.pendingWrite) {
    if (this.bufferWrites) {
      if (cb != null)
        console.error('using callbacks in buffered mode?');
      var originalCallback = this.pendingWrite;
      this.pendingWrite = function() {
        if (originalCallback)
          originalCallback();
        this.write(data, cb);
      }.bind(this);
    }
    else {
      console.error('write is already in progress!')
    }
  }
  if (cb == null) {
    if (!this.bufferWrites)
      console.error('write callback is null?');
    cb = function() {}
  }
  this.pendingWrite = cb;
  if (hasChromeSockets) {
    chrome.sockets.tcp.send(this.socketId, data, function(writeInfo) {
      // must read this to prevent log spam
      chrome.runtime.lastError;

      if (!writeInfo || writeInfo.resultCode) {
        // how to handle this?
        delete this.pendingWrite;
        return;
      }
      if (writeInfo.bytesSent < data.byteLength) {
        this.write(data.slice(writeInfo.bytesSent), cb);
      }
      else {
        delete this.pendingWrite;
        cb();
      }
    }.bind(this));
  }
  else {
    if (!this.ns)
      return;
    if (!data.byteLength) {
      process.nextTick(function() {
        delete this.pendingWrite;
        cb();
      }.bind(this));
      return;
    }
    this.ns.write(Buffer.from(data), function() {
      delete this.pendingWrite;
      cb();
    }.bind(this));
  }
}

Socket.prototype.destroy = function(data, cb) {
  if (hasChromeSockets) {
    chrome.sockets.tcp.close(this.socketId, function() {
      // must read this to prevent log spam
      chrome.runtime.lastError;
    });
  }
  else {
    this.dataReceived(null);
    if (this.ns) {
      this.ns.destroy();
      delete this.ns;
    }
  }
}

Socket.prototype.unshift = function(buffer) {
  if (buffer.byteLength == 0)
    return;
  if (!this.pending)
    this.pending = [buffer];
  else
    this.pending.unshift(buffer);
  if (!this.bufferedLength)
    this.bufferedLength = 0;
  this.bufferedLength += buffer.length;
}

Socket.prototype.dataReceived = function(payload) {
  if (payload) {
    if (payload.asUint8Array)
      payload = payload.asUint8Array();
    if (payload.constructor == ArrayBuffer)
      payload = new Uint8Array(payload);
  }
  // payload is a Uint8Array
  // push any data we got into our queue
  if (payload && payload.length) {
    var arr = new Uint8Array(payload);
    if (!this.pending)
      this.pending = [arr]
    else
      this.pending.push(arr);
  }
  if (payload == null) {
    this.closed = true;
  }
  else {
    if (!this.bufferedLength)
      this.bufferedLength = 0;
    this.bufferedLength += payload.length;
  }
  // don't trigger callbacks if there's no data or if paused
  if (this.paused || !this.pending || !this.pending.length) {
    // if this socket died, let's alert.
    var cb = this.onClose;
    if (this.closed && cb) {
      delete this.onClose;
      cb();
    }
    return;
  }
  // alert the data callback
  var pl = this.pendingLength;
  var cb = this.pendingCallback;
  if (cb) {
    delete this.pendingCallback;
    this.read(pl, cb);
  }
}

