Socket.prototype.pause = function() {
  if (this.paused) {
    return;
  }
  this.paused = true;
  this.onPause();
}

Socket.prototype.resume = function() {
  if (!this.paused) {
    return;
  }
  this.paused = false;
  this.onResume();
}

Socket.prototype.onResume = function() {
  chrome.sockets.tcp.setPaused(this.socketId, false, function() {})
}

Socket.prototype.onPause = function() {
  chrome.sockets.tcp.setPaused(this.socketId, true, function() {})
}



function Server() {
}
Server.listeners = {};

Server.prototype.__proto__ = Socket.prototype;

Server.prototype.destroy = function() {
  if (hasChromeSockets) {
    chrome.sockets.tcpServer.close(this.socketId, function() {
      chrome.runtime.lastError;
    });
  }
  else {
    if (this.ns) {
      this.ns.close();
      delete this.ns;
    }
  }
}

Server.prototype.listen = function(args, cb, listening) {
  var port;
  var address;
  if (args.constructor.name == 'Number') {
    port = args;
    address = '0.0.0.0';
  }
  else {
    address = args.address;
    port = args.port;
  }
  if (hasChromeSockets) {
    chrome.sockets.tcpServer.create(function(createInfo) {
      this.socketId = createInfo.socketId;
      Server.listeners[this.socketId] = cb;
      chrome.sockets.tcpServer.listen(createInfo.socketId, address, port, function(result) {
        // must read this to prevent log spam
        chrome.runtime.lastError;
        if (result) {
          this.destroy();
          if (listening) {
            listening(result);
          }
          return;
        }
        chrome.sockets.tcpServer.getInfo(this.socketId, function(info) {
          this.localAddress = info.localAddress;
          this.localPort = info.localPort;

          if (listening) {
            listening(result);
          }
        }.bind(this));
      }.bind(this))
    }.bind(this))
  }
  else {
    this.ns = __non_webpack_require__('net').createServer(function(s) {
      cb(new Socket({ns: s}))
    }.bind(this));
    this.ns.on('close', function() {
      this.destroy();
    }.bind(this))
    this.ns.on('error', function(e) {
      if (listening)
        listening(e);
    }.bind(this))
    this.ns.on('listening', function() {
      var info = this.ns.address();
      this.localAddress = info.address;
      this.localPort = info.port;

      if (listening)
        listening();
    }.bind(this))
    this.ns.listen({
      port: port,
      host: address,
    })
  }
}

exports.Socket = Socket;
exports.Server = Server;
