function getDefaultDeviceSettings() {
  var defaults = {
    showSoftKeys: true,
    pinTitleBar: true,
    alwaysOnTop: false,
    bitrate: 0,
    resolution: 0,
    decoder: 0,
    displaySettings: commonDisplaySettings[0],
    dimDisplay: false,
  };
  return defaults;
}

function sanitizeDeviceSettings(deviceSettings) {
  if (!deviceSettings)
    return null;
  if (deviceSettings.friendlyName && !deviceSettings.friendlyName.length)
    delete deviceSettings.friendlyName;
  return deviceSettings;
}

function loadAllDeviceSettings(cb) {
  if (window.chrome && window.chrome.storage) {
    chrome.storage.local.get('device-settings', function(vals) {
      cb(vals['device-settings'] || {});
    });
  }
  else {
    cb({});
  }
}

function loadDeviceSettings(serialno, cb) {
  if (!serialno)
    serialno = 'web';
  var defaults = getDefaultDeviceSettings();
  if (serialno == 'inkwire') {
    defaults.pinTitleBar = false;
    defaults.showSoftKeys = false;
  }
  loadAllDeviceSettings(function(vals) {
    cb(sanitizeDeviceSettings(vals[serialno] || defaults));
  });
}

function saveDeviceSettings(serialno, deviceSettings, cb) {
  sanitizeDeviceSettings(deviceSettings);
  if (!window.chrome || !window.chrome.storage) {
    if (cb)
      cb();
    return;
  }

  if (!serialno || serialno.indexOf('127.0.0.1') != -1) {
    if (cb)
      cb();
    return;
  }

  loadAllDeviceSettings(function(vals) {
    vals[serialno] = deviceSettings;
    chrome.storage.local.set({
      'device-settings': vals
    }, cb);
  })
}

function populateDisplaySettings() {
  for (var i in commonDisplaySettings) {
    var m = commonDisplaySettings[i];
    var o = new Option();
    $(o).html(m.name);
    $("#display-settings").append(o);
  }
}

function applyDeviceSettings(deviceSettings) {
  // must be || null cause undefined doesn't trigger a change
  $('#new-display-name').prop('value', deviceSettings.friendlyName || null);
  $("#softkeys-check").prop('checked', deviceSettings.showSoftKeys ? true : false).change();
  $("#pin-title-check").prop('checked', deviceSettings.pinTitleBar ? true : false).change();
  $("#always-on-top-check").prop('checked', deviceSettings.alwaysOnTop ? true : false).change();
  $("#bitrate").prop('selectedIndex', deviceSettings.bitrate || 0);
  $("#decoder").prop('selectedIndex', deviceSettings.decoder || 0);
  $("#resolution").prop('selectedIndex', deviceSettings.resolution || 0);

  var dname = deviceSettings.displaySettings && deviceSettings.displaySettings.name;
  var matched = 0;
  for (var i in commonDisplaySettings) {
    var m = commonDisplaySettings[i];
    if (m.name == dname) {
      matched = i;
      break;
    }
  }

  $("#display-settings").prop('selectedIndex', matched);
  $("#dim-display").prop('checked', deviceSettings.dimDisplay ? true : false);
  $("#auto-audio").prop('checked', deviceSettings.autoAudio ? true : false);
}

