const {Socket, Server} = require('../lib/socket/socket');
const {nextTick, readString, str2ab, ab2str, make4Len16, appendBuffer, hex2b64, showNotification} = require('../lib/base/util');
const AdbChromeUsbTransport = require('./adb-chrome-usb');
const AdbWebUsbTransport = require('./adb-web-usb');

let getUserSelectedDevices;
if (window.chrome && window.chrome.usb) {
  getUserSelectedDevices = AdbChromeUsbTransport.getUserSelectedDevices;
}
else {
  getUserSelectedDevices = AdbWebUsbTransport.getUserSelectedDevices;
}

function AdbTcpTransport(socket) {
  this.socket = socket;
  this.zero_mask = (1 << 30) - 1;
  this.type = 'tcp';
  socket.onClose = function() {
    var cb = this.currentRead;
    if (cb) {
      delete this.currentRead;
      cb({
        resultCode: -1
      })
    }
  }.bind(this);
}

AdbTcpTransport.prototype.destroy = function() {
  this.socket.destroy();
}

AdbTcpTransport.prototype.write = function(data, callback) {
  if (this.writing) {
    if (!this.pendingWrites)
      this.pendingWrites = [];
    this.pendingWrites.push({data: data, callback: callback});
    return;
  }

  this.writing = true;
  this.socket.write(data, function() {
    this.writing = false;
    callback({
      resultCode: 0
    })
    if (!this.pendingWrites)
      return;
    var next = this.pendingWrites.shift();
    if (!this.pendingWrites.length)
      this.pendingWrites = null;
    this.write(next.data, next.callback);
  }.bind(this));
}

AdbTcpTransport.prototype.read = function(len, cb) {
  this.currentRead = cb;
  this.socket.read(len, function(data) {
    delete this.currentRead;
    cb({
      resultCode: 0,
      data: data.buffer.slice(data.byteOffset, data.byteOffset + data.byteLength)
    })
  });
}

function AdbDevice(transport, cb) {
  this.onConnected = cb;
  this.transport = transport;
  this.currentSocketId = 0;
  this.sockets = {};
  this.forwards = {};
  this.maxPayload = AdbDevice.MAX_PAYLOAD;
}

AdbDevice.prototype.fatal = function(e) {
  console.log('fatal error', JSON.stringify(e));
  var cb = this.onConnected;
  if (cb) {
    delete this.onConnected;
    cb();
  }
  else if (this.onError) {
    this.onError();
    delete this.onError;
  }
  this.destroy();
}

AdbDevice.prototype.destroy = function() {
  for (var socket in this.sockets) {
    socket = this.sockets[socket];
    socket.dataReceived(null);
  }
  if (this.forwards) {
    $.each(this.forwards, function(port, listener) {
      listener.destroy();
    })
  }
  this.transport.destroy();
}

AdbDevice.kCommandSYNC = 0x434e5953,
AdbDevice.kCommandCNXN = 0x4e584e43,
AdbDevice.kCommandOPEN = 0x4e45504f,
AdbDevice.kCommandOKAY = 0x59414b4f,
AdbDevice.kCommandCLSE = 0x45534c43,
AdbDevice.kCommandWRTE = 0x45545257,
AdbDevice.kCommandAUTH = 0x48545541

AdbDevice.kAuthToken = 1;
AdbDevice.kAuthSignature = 2;
AdbDevice.kAuthRSAPublicKey = 3;

// ADB protocol version
AdbDevice.ADB_PROTOCOL_VERSION = 0x01000000;
// ADB Version
AdbDevice.ADB_VERSION = 40;
AdbDevice.MAX_PAYLOAD = 4096;

AdbDevice.checksum = function(data) {
  data = new Uint8Array(data);
  var sum = 0;
  for (var i = 0; i < data.byteLength; i++) {
    sum += data[i];
  }
  return sum & 0xFFFFFFFF;
}

AdbDevice.prototype.sendMessage = function(command, arg0, arg1, payload, cb) {
  // Header (24):
  // command
  // arg0
  // arg1
  // body length
  // checksum
  // command ^ 0xffffffff

  if (!payload) {
    payload = '';
  }

  if (payload.constructor.name == 'String') {
    payload = str2ab(payload);
  }

  var appendZero = true;
  if (!payload.byteLength) {
    appendZero = false;
  }
  if (command == AdbDevice.kCommandAUTH && arg0 == AdbDevice.kAuthSignature) {
    appendZero = false;
  }
  if (command == AdbDevice.kCommandWRTE) {
    appendZero = false;
  }

  var bodyLength = payload.byteLength;
  if (appendZero) {
    bodyLength++;
  }

  if (appendZero) {
    var copy = new ArrayBuffer(payload.byteLength + 1);
    var view = new Uint8Array(copy);
    view.set(new Uint8Array(payload));
    view[copy.byteLength - 1] = 0;
    payload = copy;
  }

  var header = new ArrayBuffer(24);
  var view = new DataView(header);

  view.setUint32(0, command, true);
  view.setUint32(4, arg0, true);
  view.setUint32(8, arg1, true);
  view.setUint32(12, bodyLength, true);
  view.setUint32(16, AdbDevice.checksum(payload), true);
  view.setUint32(20, command ^ 0xFFFFFFFF, true);

  // new Uint8Array(data, 24).set(new Uint8Array(payload));

  this.transport.write(header, function(result) {
    if (result.resultCode) {
      this.fatal(result);
    }
    if (!payload.byteLength && cb) {
      cb();
    }
  }.bind(this));

  if (payload.byteLength) {
    this.transport.write(payload, function(result) {
      if (result.resultCode) {
        this.fatal(result);
      }
      if (cb) {
        cb();
      }
    }.bind(this));
  }
}

AdbDevice.prototype.getKey = function(cb) {
  if (!window.chrome || !window.chrome.storage) {
    var key = new JSEncrypt({default_key_size: 2048});
    try {
      var keyString = localStorage.getItem('adbkey');
      if (!keyString)
        throw new Error();
      var adbkey = JSON.parse(keyString);
      key.setPrivateKey(adbkey);
    }
    catch (e) {
      adbkey = key.getPrivateKeyB64();
      key.setPrivateKey(adbkey);
      localStorage.setItem('adbkey', JSON.stringify(adbkey));
    }
    nextTick(() => cb(key));
    return;
  }

  chrome.storage.local.get('adbkey', function(result) {
    var adbkey = result.adbkey;
    var key = new JSEncrypt({default_key_size: 2048});
    if (!adbkey) {
      adbkey = key.getPrivateKeyB64();
      key.setPrivateKey(adbkey);
      chrome.storage.local.set({'adbkey': adbkey})
    }
    else {
      key.setPrivateKey(adbkey);
    }
    cb(key);
  })
}

AdbDevice.prototype._convertToMinCrypt = function(rsaKey) {
  var bitLength = 2048;//rsaKey.n.toByteArray().length * 8;
  var numWords = bitLength / 8 / 4;

  // B32 = 1 << 32 or 0x100000000L
  var B32 = BigInteger.ONE.shiftLeft(32);

  // modulus
  var N = rsaKey.n.clone();

  // R=2^2048
  var R = BigInteger.ONE.shiftLeft(1).pow(bitLength);

  // RR = R^2 % N
  var RR = R.multiply(R).mod(N);

  // Format:
  //   int32 len
  //   uint32 n0inv
  //   uint32 n[numWords]
  //   uint32 rr[numWords]
  //   int32 exponent
  var pkey = new Uint32Array(3 + numWords * 2);
  pkey[0] = numWords;
  pkey[1] = B32.subtract(N.modInverse(B32)).intValue();

  var iEnd = numWords + 2;
  for (var i = 2, j = 2 + numWords; i < iEnd; ++i, ++j) {
    pkey[i] = N.mod(B32).intValue();
    N = N.divide(B32);
    pkey[j] = RR.mod(B32).intValue();
    RR = RR.divide(B32);
  }

  pkey[pkey.length - 1] = rsaKey.e;

  // Convert to hex string first since our Base64 only does that
  var hexStr = "";
  var u8view = new Uint8Array(pkey.buffer);
  for (var i = 0; i < u8view.length; ++i) {
    var digit = u8view[i].toString(16);
    if (digit.length == 1) {
      hexStr += "0";
    }
    hexStr += digit;
  }

  // TODO: allow setting a useful identifier
  return hex2b64(hexStr) + " adb@chrome";
};

AdbDevice.prototype.sign = function(rsakey, data) {
  if (rsakey == null) {
    throw "AuthManager is not initialized";
  }

  var totalLen = 2048 / 8;//rsakey.n.toByteArray() * 4;

  var array = new Uint8Array(totalLen);
  array[0] = 0x00;
  array[1] = 0x01;

  var ASN1_PREAMBLE = [0x00, 0x30, 0x21, 0x30, 0x09, 0x06, 0x05, 0x2B, 0x0E, 0x03, 0x02, 0x1A, 0x05, 0x00, 0x04, 0x14];

  // Subtract the preamble and hash length.
  var padEnd = totalLen - ASN1_PREAMBLE.length - data.byteLength;
  for (var i = 2; i < padEnd; i++) {
    array[i] = 0xFF;
  }

  array.set(new Uint8Array(ASN1_PREAMBLE), padEnd);
  padEnd += ASN1_PREAMBLE.length;
  array.set(new Uint8Array(data), padEnd);

  var msg = new BigInteger(Array.apply([], array));
  return new Uint8Array(rsakey.doPrivate(msg).toByteArray()).buffer;
};

function parseConnectionPayload(payload) {
  var ret = {};
  if (payload.constructor.name != 'String')
    payload = ab2str(payload);
  var splits = payload.replace('device::', '').split(';');
  for (var split in splits) {
    split = splits[split];
    var parts = split.split('=');
    if (parts.length == 2) {
      ret[parts[0]] = parts[1];
    }
  }
  return ret;
}

AdbDevice.parseConnectionPayload = parseConnectionPayload;

AdbDevice.prototype.handleUnknown = function(localId, remoteId) {
  console.log('no idea what this socket is.');
  this.sendMessage(AdbDevice.kCommandCLSE, localId, remoteId);
}

AdbDevice.prototype.handleMessage = function(header, payload) {
  var command = header.getUint32(0, true);
  var arg0 = header.getUint32(4, true);
  var arg1 = header.getUint32(8, true);

  switch (command) {
  case AdbDevice.kCommandOPEN:
    if (this.onOpenSocket)
      this.onOpenSocket(payload, arg0)
    break;
  case AdbDevice.kCommandAUTH:
    console.log('auth:', this);
    // get the key and respond
    this.getKey(function(rsakey) {
      if (this.sentSignature) {
        var publicKey = this._convertToMinCrypt(rsakey.getKey());
        this.sendMessage(AdbDevice.kCommandAUTH, AdbDevice.kAuthRSAPublicKey, 0, publicKey);
        showNotification('Check your Android device and click "Allow USB Debugging".');
      }
      else {
        this.sentSignature = true;
        var signed = this.sign(rsakey.getKey(), payload);
        this.sendMessage(AdbDevice.kCommandAUTH, AdbDevice.kAuthSignature, 0, signed, function() {
          // console.log(arguments);
        });
      }
    }.bind(this))
    break;
  case AdbDevice.kCommandOKAY:
    var remoteId = arg0;
    var localId = arg1;
    var socket = this.sockets[localId];
    if (!socket) {
      this.handleUnknown(localId, remoteId);
      return;
    }
    var cb = socket.onConnected;
    if (cb) {
      delete socket.onConnected;
      socket.remoteId = remoteId;
      cb(socket);
    }

    // see if we need to pump more data out
    var data = socket.pendingWrite;
    if (data) {
      cb = socket.wrote;
      delete socket.wrote;
      delete socket.pendingWrite;
      socket.write(data, cb);
      return;
    }

    // this socket can write more...
    cb = socket.wrote;
    if (cb) {
      delete socket.wrote;
      cb();
    }
    break;
  case AdbDevice.kCommandCNXN:
    this.rawProperties = ab2str(payload);
    this.properties = parseConnectionPayload(payload)
    var cb = this.onConnected;
    if (cb) {
      delete this.onConnected;
      cb(this);
    }
    break;
  case AdbDevice.kCommandWRTE:
    var remoteId = arg0;
    var localId = arg1;
    var socket = this.sockets[localId];
    if (!socket) {
      this.handleUnknown(localId, remoteId);
      return;
    }
    // if not paused, ask for more
    if (!socket.paused) {
      this.sendMessage(AdbDevice.kCommandOKAY, socket.localId, socket.remoteId);
    }
    socket.dataReceived(new Uint8Array(payload));
    break;
  case AdbDevice.kCommandCLSE:
    var remoteId = arg0;
    var localId = arg1;
    var socket = this.sockets[localId];
    if (!socket) {
      console.log('asked to close unknown socket?');
      return;
    }
    delete this.sockets[localId];
    socket.destroy();
    // closed without connecting
    var cb = socket.onConnected;
    if (cb) {
      delete socket.onConnected;
      cb();
    }
    break;
  default:
    console.log('unknown command: ', command.toString(16), arg0, arg1, payload);
    break;
  }
}

AdbDevice.prototype.onReceiveMessage = function(result) {
  if (result.resultCode) {
    this.fatal(result);
    return;
  }

  var header = new DataView(result.data);
  var transferLen = header.getUint32(12, true);

  if (!transferLen) {
    try {
      this.handleMessage(header, null);
    }
    finally {
      this.receiveMessages();
    }
    return;
  }

  this.transport.read(transferLen, function(result) {
    if (result.resultCode) {
      this.fatal(result);
      return;
    }

    var payload = result.data;
    if (AdbDevice.checksum(payload) != header.getUint32(16, true)) {
      this.receiveMessages();
      return;
    }

    try {
      this.handleMessage(header, payload);
    }
    finally {
      this.receiveMessages();
    }
  }.bind(this))
}

AdbDevice.prototype.receiveMessages = function() {
  this.transport.read(24, this.onReceiveMessage.bind(this));
}

AdbDevice.prototype.forwardPort = function(args) {
  var forwardingServer = new Server();
  forwardingServer.listen({
    port: args.fromPort,
    address: '127.0.0.1'
  }, function(localSocket) {
    this.newSocket(args.to, function(remoteSocket) {
      if (remoteSocket)
        Socket.stream(localSocket, remoteSocket);
      else
        localSocket.destroy();
    }.bind(this))
  }.bind(this), function() {
    this.forwards[args.fromPort] = forwardingServer;
  }.bind(this));
}

AdbDevice.prototype.newAdbSocket = function(socketId, cb) {
  var socket;
  if (this.createSocket)
    socket = this.createSocket(socketId, cb);
  else
    socket = new AdbSocket(this, socketId, cb);
  return socket
}

AdbDevice.prototype.newSocket = function(service, cb) {
  var socketId = ++this.currentSocketId;
  this.sockets[socketId] = this.newAdbSocket(socketId, cb);
  this.sendMessage(AdbDevice.kCommandOPEN, socketId, 0, service);
}

function AdbSocket(device, socketId, cb) {
  if (!cb) {
    cb = function() {
    }
  }

  this.device = device;
  this.localId = socketId;
  this.onConnected = cb;
}

AdbSocket.prototype.write = function(data, cb) {
  if (this.pendingWrite || this.wrote) {
    console.log('bad adb socket state, already writing');
    throw new Error('bad adb socket state, already writing');
  }

  var toWrite = Math.min(this.device.transport.zero_mask, this.device.maxPayload);
  if (toWrite < data.byteLength) {
    this.pendingWrite = data.slice(toWrite);
    data = data.slice(0, toWrite);
  }
  else {
    this.pendingWrite = null;
  }
  this.wrote = cb;
  this.device.sendMessage(AdbDevice.kCommandWRTE, this.localId, this.remoteId, data);
}

AdbSocket.prototype.destroy = function() {
  this.device.sendMessage(AdbDevice.kCommandCLSE, this.localId, this.remoteId);
  this.dataReceived(null);
}

AdbSocket.prototype.buffered = Socket.prototype.buffered;
AdbSocket.prototype.dataReceived = Socket.prototype.dataReceived;
AdbSocket.prototype.read = Socket.prototype.read;
AdbSocket.prototype.pause = Socket.prototype.pause;
AdbSocket.prototype.resume = Socket.prototype.resume;
AdbSocket.prototype.unshift = Socket.prototype.unshift;

AdbSocket.prototype.onPause = function() {
  // no op
}

AdbSocket.prototype.onResume = function() {
  // tell the other side we want data again
  this.device.sendMessage(AdbDevice.kCommandOKAY, this.localId, this.remoteId);
}

function connectUsbAdb(device, iface, cb) {
  console.log('connecting');
  var adb = new AdbDevice(device.newTransport(iface), cb);
  console.log('sending CNXN');
  adb.sendMessage(AdbDevice.kCommandCNXN, AdbDevice.ADB_PROTOCOL_VERSION, AdbDevice.MAX_PAYLOAD, "host::");
  console.log('starting receive loop');
  adb.receiveMessages();
}

function AdbServer(options) {
  var options = options || {};
  var port = options.port || 5037;
  var start = options.start !== false;

  this.currentSocketId = 1;
  this.pendingDevices = {};
  this.port = port;
  this.adbDevices = {};
  this.clients = {};
  if (start) {
    this.start();
  }
}

AdbServer.prototype.onOpenSocket = function(payload, remoteSocketId) {
  var device = this;
  var destination = ab2str(payload);
  var split = destination.split(':');
  var port = Number.parseInt(split[1]);
  Socket.connect({host: '127.0.0.1', port: port}, function (socket) {
    if (!socket) {
      device.sendMessage(AdbDevice.kCommandOKAY, 0, remoteSocketId);
      return;
    }

    var socketId = ++device.currentSocketId;
    var adbSocket = device.newAdbSocket(socketId);
    adbSocket.remoteId = remoteSocketId;
    device.sockets[socketId] = adbSocket;
    device.sendMessage(AdbDevice.kCommandOKAY, socketId, remoteSocketId);
    Socket.stream(socket, adbSocket);
  });
}

function _nowMs() {
  return new Date().getTime();
}

AdbServer.prototype.start = function() {
  if (this.server) {
    console.log('ADB Server started while already started');
    return;
  }
  this.clients = {};
  this.adbDevices = {};
  this.pendingDevices = {};
  this.refreshing = {};
  var server = new Server();
  if (this.dummyListen)
    return;
  server.listen({
    port: this.port,
    address: '127.0.0.1'
  }, function(socket) {
    var client = new AdbClient(this, socket);
    var socketId = ++this.currentSocketId;
    this.clients[socketId] = client;
    socket.onClose = function() {
      delete this.clients[socketId];
    }.bind(this);
    client.receiveHeader();
  }.bind(this),
  function(result) {
    if (result) {
      console.log('adb server failed to listen: ' + result);
      return;
    }
    console.log('ADB Server started');
    this.server = server;
  }.bind(this));
}

AdbServer.prototype.isRunning = function() {
  return this.server != null;
}

AdbServer.prototype.kill = function() {
  // kill listener
  this.server.destroy();
  this.server = null;
  this.refreshing = {};
  // kill clients
  for (var client in this.clients) {
    client = this.clients[client];
    client.socket.destroy();
  }
  this.clients = {};
  // kill clients
  for (var device in this.adbDevices) {
    device = this.adbDevices[device];
    device.destroy();
  }
  this.adbDevices = {};
  this.pendingDevices = {};
}

AdbServer.prototype.withAdbDevice = function(adb, cb) {
  adb.onError = function() {
    delete this.adbDevices[adb.serialno];
    this.internalOnDevicesChanged();
  }.bind(this);

  var withSerial = function(str) {
    adb.serialno = str.trim();
    this.adbDevices[adb.serialno] = adb;
    console.log('found device: ' + adb.serialno);
    this.internalOnDevicesChanged();
    cb(adb);
  }.bind(this);

  if (adb.serialno) {
    withSerial(adb.serialno);
    return;
  }

  adb.newSocket('shell:getprop ro.serialno', function(adbSocket) {
    readString(adbSocket, function(str) {
      withSerial(str);
    }.bind(this))
  }.bind(this))
}

AdbServer.prototype.tryDevice = function(device, cb) {
  var serialno = device.serialno;
  var pending = this.pendingDevices;
  var server = this;

  function tryInterface(i) {
    console.log('trying intrface', i);
    var number = i.interfaceNumber;

    // check pending
    if (pending[number]) {
      return false;
    }

    pending[number] = device;

    console.log('claiming:', JSON.stringify(i));
    device.claimInterface(i.interfaceNumber)
    .then(() => {
      console.log('claimed interface');

      connectUsbAdb(device, i, function(adb) {
        console.log("usb adb connected");
        if (!adb) {
          delete pending[number];
          cb();
          return;
        }

        adb.serialno = serialno;
        adb.onOpenSocket = server.onOpenSocket;

        server.withAdbDevice(adb, function(adb) {
          delete pending[number];
          cb(adb);
        });
      })
    })
    .catch(e => {
      console.log('unable claim interface', JSON.stringify(e));
    })
    return true;
  }

  device.listInterfaces()
  .then(interfaces => {
    console.log('got interfaces', JSON.stringify(interfaces))
    var usedHandle = false;
    for (var i in interfaces) {
      i = interfaces[i];

      if (i.interfaceClass == 0xff && i.interfaceSubclass == 0x42 && i.interfaceProtocol == 0x1) {
          usedHandle |= tryInterface(i);
      }
    }
    if (!usedHandle) {
      device.close();
    }
  })
  .catch(e => {
      console.log('unable list interfaces', e);
      if (cb)
        cb();
  })
}


AdbServer.prototype.refreshDevice = function(device, cb) {
  device.open()
  .then(() => {
    this.start();
    console.log('opened ' + device.serialNumber);
    this.tryDevice(device, (adb) => {
      console.log('try device done', adb);
      if (adb) {
        adb.usbDevice = device.device;
      }
      cb(adb);
    })
  })
  .catch(e => {
    console.log('unable to open device', JSON.stringify(e));
    cb()
  });
}

AdbServer.prototype.internalOnDevicesChanged = function() {
  for (var client in this.clients) {
    client = this.clients[client];
    if (!client.tracking)
      continue;
    var payload = client.getDevicesString();
    if (!payload.length)
      payload = '0000\n'
    client.socket.write(str2ab(payload), function() {})
  }
}

AdbServer.prototype.stop = function() {
  this.server.destroy();
}

function AdbClient(server, socket) {
  this.server = server;
  this.socket = socket;
}

AdbClient.prototype.resolveTransport = function(transport, serialno) {
  if (serialno) {
    var ret = this.server.adbDevices[serialno];
    if (!ret)
      return 'device not found';
    return ret;
  }
  var num = Object.keys(this.server.adbDevices);
  if (num > 1) {
    return "more than one device";
  }
  if (num == 0) {
    return 'no devices connected';
  }
  for (var device in this.server.adbDevices) {
    return this.server.adbDevices[device];
  }
}

AdbClient.prototype.write = function(data, status, cb) {
  if (!status) {
    status = 'OKAY';
  }
  // console.log(data);
  data = str2ab(data);
  var len = data.byteLength;
  var len16 = make4Len16(len);
  len16 = str2ab(status + len16);
  var payload = appendBuffer(new Uint8Array(len16), new Uint8Array(data)).buffer;
  if (!cb) {
    cb = function() {
      this.socket.destroy();
    }.bind(this);
  }
  this.socket.write(payload, cb)
}

AdbClient.prototype.getDevicesString = function(options) {
  var options = options || {};
  var longformDevices = options.longformDevices;
  var devices = '';
  for (var device in this.server.adbDevices) {
    device = this.server.adbDevices[device];
    devices += device.serialno + '\tdevice';
    if (longformDevices) {
      if (device.transport.type == 'usb')
        devices += ' usb:' + device.transport.iface.interfaceNumber;
      else
        devices += ' tpcip:' + 'something';
      devices += ' product:' + device.properties['ro.product.name'];
      devices += ' model:' + device.properties['ro.product.model'];
      devices += ' device:' + device.properties['ro.product.device'];
    }
    devices += '\n';
  }
  return devices;
}

AdbClient.prototype.writeDevices = function(options, cb) {
  this.write(this.getDevicesString(options), null, cb);
}

AdbClient.prototype.handlePayload = function(data) {
  data = ab2str(data);
  // console.log("handling command: " + data);
  var commandParts = data.split(':');
  var hostCmd = data;
  var serialno;
  if (commandParts[0] == 'host-serial') {
    commandParts[0] = 'host';
    serialno = commandParts.splice(1, 1)[0];
    if (Number.isInteger(parseInt(commandParts[1]))) {
      // this is in host:port format
      serialno += ':' + commandParts.splice(1, 1)[0];
    }
  }
  if (commandParts.length >= 2)
    hostCmd = commandParts[0] + ':' + commandParts[1];

  switch (hostCmd) {
  case 'host:version':
    this.write(make4Len16(AdbDevice.ADB_VERSION));
    break;
  case 'host:devices-l':
  case 'host:devices':
    var longformDevices = data == 'host:devices-l';
    this.writeDevices({ longformDevices: longformDevices });
    break;
  case 'host:features':
    var transport = this.resolveTransport(data, serialno);
    if (transport.constructor.name == 'String') {
      this.write(transport, 'FAIL');
      break;
    }
    var features = transport.properties.features;
    if (!features)
      features = '';
    this.write(make4Len16(features));
    break;
  case 'host:transport-usb':
  case 'host:transport-any':
    var transport = this.resolveTransport(data, serialno);
    if (transport.constructor.name == 'String') {
      this.write(transport, 'FAIL');
      break;
    }
    this.transport = transport;
    this.socket.write(str2ab('OKAY'), function() {})
    break;
  case 'host:kill':
    this.server.kill();
    break;
  case 'host:disconnect':
    if (commandParts.length > 4) {
      this.write('host:disconnect only takes 1 argument', 'FAIL');
      break;
    }
    if (commandParts.length > 2) {
      var remoteHost = commandParts[2];
      if (remoteHost.length) {
        var remotePort = 5555;
        if (commandParts.length > 3)
          remotePort = Number.parseInt(commandParts[3]);
        var remote = remoteHost + ':' + remotePort;
        var device = this.server.adbDevices[remote];
        if (device && device.transport.type == 'tcp')
          device.destroy();
        this.write('disconnected');
        break;
      }
    }
    for (var device in this.server.adbDevices) {
      device = this.server.adbDevices[device];
      if (device.transport.type == 'tcp')
        device.destroy();
    }
    this.write('disconnected');
    break;
  case 'host:connect':
    if (commandParts.length < 3) {
      this.write('need more arguments for connect <host>[:<port>]', 'FAIL');
      break;
    }
    var remoteHost = commandParts[2];
    var remotePort = 5555;
    if (commandParts.length > 3)
      remotePort = Number.parseInt(commandParts[3]);
    Socket.connect({host: remoteHost, port: remotePort}, function(socket, e) {
      if (!socket) {
        console.error('connect failed');
        this.write('unable to connect to ' + remoteHost + ' ' + remotePort + ': ' + e, 'FAIL');
        return this;
      }
      var adb = new AdbDevice(new AdbTcpTransport(socket), function(adb) {
        this.server.withAdbDevice(adb, function() {
          var response = 'connected to ' + remoteHost + ':' + remotePort;
          console.log(response);
          this.write(response);
        }.bind(this))
      }.bind(this));
      adb.onOpenSocket = this.onOpenSocket;
      adb.serialno = remoteHost + ':' + remotePort;
      socket.onClose = function() {
        adb.fatal('socket closed');
      }.bind(this);
      adb.sendMessage(AdbDevice.kCommandCNXN, AdbDevice.ADB_PROTOCOL_VERSION, AdbDevice.MAX_PAYLOAD, "host::");
      adb.receiveMessages();
    }.bind(this))
    break;
  case 'host:track-devices':
    this.tracking = _nowMs();
    this.writeDevices({}, function() {});
    break;
  case  'host:forward':
    // host:forward:tcp:5000;tcp:5000
    var forwardParts = commandParts.join(':').substring(hostCmd.length + 1).split(';');
    var from = forwardParts[0].split(':');
    var fromPort = parseInt(from[1]);
    var transport = this.resolveTransport(data, serialno);
    if (transport.constructor.name == 'String') {
      this.write(transport, 'FAIL');
      break;
    }
    transport.forwardPort({
      fromPort: fromPort,
      to: forwardParts[1]
    })
    this.socket.write(str2ab('OKAYOKAY'), function() {
      // this.socket.destroy();
    }.bind(this));
    break;
  default:
    // check to see if a smart socket is pending
    if (this.transport) {
      var transport = this.transport;
      transport.newSocket(data, function(socket) {
        if (!socket) {
          this.socket.write(str2ab('OKAY'), function() {
            this.socket.destroy();
          }.bind(this))
          return
        }
        this.socket.write(str2ab('OKAY'), function() {})
        Socket.stream(socket, this.socket);
      }.bind(this));
      return;
    }

    // check if this was a smart socket request for a specific device
    var specific = 'host:transport:';
    if (data.startsWith(specific)) {
      var serialno = data.substr(specific.length);
      var device = this.server.adbDevices[serialno];
      if (!device) {
        this.write('device not found', 'FAIL');
        return;
      }
      this.transport = device;
      this.socket.write(str2ab('OKAY'), function() {})
      break;
    }

    // WAT
    console.log('unknown request: ' + data);
    this.write('unknown command: ' + data, 'FAIL');

    showNotification(appName + "\'s adb server encountered an unknown adb command.\nYou may want to close " + appName + " and start your adb binary manually.");
    break;
  }
  this.receiveHeader();
}

AdbClient.prototype.receivePayload = function(data) {
  var len = parseInt(ab2str(data), 16);
  this.socket.read(len, this.handlePayload.bind(this));
}

AdbClient.prototype.receiveHeader = function() {
  this.socket.read(4, this.receivePayload.bind(this));
}

exports.AdbDevice = AdbDevice;
exports.AdbServer = AdbServer;
exports.AdbTcpTransport = AdbTcpTransport;
exports.getUserSelectedDevices = getUserSelectedDevices;
